import Conf from '../config/config';
import Utils from '../utils/utils';

class Slm {
  constructor({
    appkey,
    adSpaceKey
  },returnSetting) {
    this.appkey = appkey;
    this.adSpaceKey = adSpaceKey;
    this.returnSetting = returnSetting;
    this.defaultPathArr = Utils.storage.get("defaultPathArr") || [];
    this.isIncludes = this.defaultPathArr.includes(this.adSpaceKey);
    this.slmNum = Utils.session.get("slmNum") || 1;
    this.isSlmUrl = Utils.getUrlParam("isSlmUrl");
    this.currentLimit = Utils.session.get("currentLimit") || "";
    if (/MicroMessenger/i.test(navigator.userAgent.toLowerCase())) {
      return;
    }

    this.initSlm();
  }

  /**
   *  slm
   * openOrClose 1  
   * pathChoose 1   
   * slm
  */
  initSlm() {

    if (Utils.session.get('eggIframe')) {
      return;
    }

    if (this.slmNum == "1" && !this.checkCurrentLimit()) {
      return;
    }

    if (!(this.slmNum == "1") && !this.isSlmUrl) {
      return;
    }

    if(this.returnSetting.openOrClose){
      if(this.returnSetting.pathChoose){
        this.customPath();
        return;
      }
      this.defaultPath();
    }
    return;
  }

  /**
   *   
   * slmNum2 
  */
  defaultPath() {

    if(this.slmNum > 2){
      return;
    }
    var pathNameList = null;

    //  
    // let isDouble11 = window.global.isDouble11;
    // if( this.slmNum == 1 && isDouble11 ){
    //   pathNameList = ["static/fulishe4"];
    // }

    this.customPathBack(pathNameList);
  }

  /**
   *   
   * changeFrequency  1   2
   * changeFrequency  1 slmNum  1 defaultPathArr adSpaceKey 
   * adSpaceKeyadSpaceKey
  */
  customPath() {
    
    if(this.returnSetting.changeFrequency == "1" && this.isIncludes && this.slmNum == "1"){
      return;
    }

    this.customPathLimit();
  };

  /**
   *  
   * changeSettingList
   * slmNumchangeSettingList
   *   
   * changeSettingList checked  0 
   * changeSettingList checked  1 pathNameList
  */
  customPathLimit() {
    let changeSettingList = this.returnSetting.changeSettingList;
    if(this.slmNum > changeSettingList.length-1 && !changeSettingList[0].checked){
      return;
    }

    if(this.slmNum > changeSettingList.length-1 && changeSettingList[0].checked){
      let pathNameList = changeSettingList[0].pathNameList;
      this.customPathBack(pathNameList);
      return;
    }

    if(!changeSettingList[this.slmNum].checked && !changeSettingList[0].checked){
      return;
    }

    if(!changeSettingList[this.slmNum].checked && changeSettingList[0].checked){
      let pathNameList = changeSettingList[0].pathNameList;
      this.customPathBack(pathNameList);
      return;
    }

    if(changeSettingList[this.slmNum].checked){
      let pathNameList = changeSettingList[this.slmNum].pathNameList;
      this.customPathBack(pathNameList);
      return;
    }
  };

  /**
   *  
   * pathNameList  gamehtml
   * 
  */
  customPathBack(pathNameList) {
    let type = 'defaultPath';
    if(pathNameList && pathNameList.length > 0){
      let random = Math.floor(Math.random()*pathNameList.length);
      type = pathNameList[random];
    }
    this.bindEvent();
    let current = window.location.href;
    window.history.replaceState({
      page: "slm",
      entered:false
    }, '', this.slmUrl(type));
    window.history.pushState({
      page: 'current'
    }, '', current);

  }

  /**
   * slm
   *   currentLimit
   *   currentLimit
   * currentLimit
  */
  checkCurrentLimit() {
    let index = window.location.pathname.split("/").indexOf("index.html") - 1;
    let returnUrl = window.location.pathname.split("/")[index];
    if(!this.currentLimit){
      Utils.session.set("currentLimit",returnUrl);
      return true;
    };

    if(this.currentLimit == returnUrl ){
      return true;
    }

    return false;
  }
  
  slmUrl(type) {
    let sfuli = `?appkey=${this.appkey}&adSpaceKey=${this.adSpaceKey}&entranceType = 2&isSlmUrl=true`;
    let s = `?appk=${this.appkey}&ask=${this.adSpaceKey}&from=back&entranceType = 2&isSlmUrl=true`;
    if(type == "defaultPath"){
      return Conf.staticURL + '/' + Conf.controllerSlm + s;
    }

    return location.origin + '/' + type + '/index.html' + sfuli
  }

  bindEvent() {
    window.onpopstate = () => { 
      if (window.history.state && window.history.state.page == 'slm') {
        if(!window.history.state.entered){
          window.history.replaceState({
            page: 'slm',
            entered: true
          }, '', window.location.href);
          setTimeout(() => {
            if(this.returnSetting.changeFrequency == "1" && !this.isIncludes){
              this.defaultPathArr.push(this.adSpaceKey);
              Utils.storage.set("defaultPathArr",this.defaultPathArr);
            }
            Utils.session.set("slmNum",Number(this.slmNum) + 1);
            window.location.reload();
          }, 0);
        }
      }
    }
  }
}

export default Slm;