import './style/doubleCase.scss'
export default class popBox {

    constructor(params) {
        this.params = params;
        this.render();
    }

    render() {
        let _this = this;
        let item = `<div class="pop_box double_case" id="pop_box" no-heatmap >
            <div class="animation_box">
                <div class="static">
                    <div class="animation_cloud"></div>
                    <div class="box"></div>
                    <div class="animation_case"></div>
                    <div class="title"></div>
                    <div class="light"></div>
                    <div class="price_info">
                        <div class="img_box">
                            <img src="https://hdggcdn.bayimob.com/hdggstatic/popBox/card.png" alt="">
                            <div class="card-bg">
                                <img src= ${this.params.ad.imageUrl} alt="">
                                <p> ${this.params.ad.advIntro} </p>
                            </div>
                        </div>
                    </div>
                    <div class="ribbons"></div>
                    <div class="double_case_goto"></div>
                </div>
            </div>
        </div>`

        $("body").append(item);
        setTimeout(function () {
            $("#pop_box .box").show();
            $('.double_case_goto').show()
        }, 400);
        setTimeout(function () {
            $("#pop_box .box").addClass("shake");
        }, 900);
        setTimeout(function () {
            $("#pop_box .box").addClass("extrusio");
        }, 1300);
        setTimeout(function () {
            $("#pop_box .animation_case").show();
            $("#pop_box .ribbons").show();
            $("#pop_box .light").show();
            $("#pop_box .animation_box").append('<span id="close" class="close-btn closetc iconfont"></span>');
            _this.bindClose();
        }, 1500);
        setTimeout(function () {
            $("#pop_box .title").show()
        }, 1900);
        setTimeout(function () {
            $("#pop_box .img_box").show();
            $("#pop_box .goto").show();
            _this.bindToAd();
        }, 2000);
        setTimeout(function () {
            $("#pop_box .ribbons").hide();
        }, 2800);
    }

    bindClose() {
        let _this = this;
        $(".pop_box #close").on("click",()=>{
            $(".pop_box").remove();
            _this.params.close();
            if (!!window.styleReset && typeof window.styleReset == 'function') {
                try {
                    window.styleReset();
                } catch (err) {}
            }
            return false;
        });
    };

    bindToAd() {
        let _this = this;
        $('.pop_box .goto,.double_case_goto').on('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            _this.params.click();
            return false;
        });

        $(".pop_box .card-bg img").on("click",(e)=>{
            e.preventDefault();
            e.stopPropagation();
            let imgs = 1;
            _this.params.click(imgs);
        });
    };

}