import './style/redbagCommon.scss';
import Request from '../core/request';
import utils from '../utils/utils'
import Noaduc from './noAdUc'
import { hdggMaterial, hdggMaterial1 } from '../popup/hdggMaterial2';
import { hdggMaterialFlod, hdggMaterialGuaka } from '../popup/materialNteraction';

const objDake = {
  hdggMaterial,
  hdggMaterial1,
  hdggMaterialFlod,
  hdggMaterialGuaka
}

export default class redBag {
  constructor(options) {
    this.options = options.options;
    console.log(this.options, '')
    this.create();
    // this.num = 0;
    // this.add_bag = ''
    // this.ads = true;
    // this.suiArr = null;
    // this.setTime = null;
    this.currentState = utils.storage.get("currentState") || {};
    this.triggerExt = options.options.triggerExt;
    this.params = options.options.options;
    this.gameArr = utils.storage.get('gameArr') || [];
    this.click = options.options.click;
    this.close = options.options.close;
    this.gameHtmlId = options.options.gameHtmlId
  }

  // html
  create() {
    let html = `
        <div class="hongbaoyu">
          <div class="guide_main">
            <span id="closeRedBag" class="close-btns closetc iconfont"></span>
            <img src="https://hdggcdn.bayimob.com/hdggstatic/redBag/btn_play.png" class="guide_btn">
          </div>
          <ul class="couten"></ul>
        </div>`;
    $('body').append(html);
    this.showGuide();
  };
  initRedBao() {
    //
    var ads = true;
    var num = 0;
    var win1 = (parseInt($(".couten").width())) - 80;
    //
    var add_bag = "";
    var add = function () {
      $('.backward').hide();
      // var hb = parseInt(Math.random() * (3 - 1) + 1);
      // var Wh = parseInt(Math.random() * (70 - 30) + 20);
      var Left = parseInt(Math.random() * (win1 - 0) + 0);
      var rot = (parseInt(Math.random() * (45 - (-45)) - 45)) + "deg";
      var imgSrc = ['https://hdggcdn.bayimob.com/hdggstatic/redBag/yuanbao.png', 'https://hdggcdn.bayimob.com/hdggstatic/redBag/redbag.png', 'https://hdggcdn.bayimob.com/hdggstatic/redBag/qipao1.png', 'https://hdggcdn.bayimob.com/hdggstatic/redBag/qipao2.png']
      // function updateRandom() {
      //   var ranNum = Math.random();
      //   var n = Math.random() / 4;
      //   if (ranNum < 0.01) {
      //     return n;
      //   }
      //   else if (ranNum < 0.04) {
      //     return n + 0.25;
      //   }
      //   else if (ranNum < 0.1) {
      //     return n + 0.5;
      //   }
      //   else if (ranNum < 1) {
      //     return n + 0.75;
      //   }
      // }
      function __gift__() {
        var __rand__ = Math.random();
        if (__rand__ < .15) { return 0 }
        else if (__rand__ < .3) { return 1 }
        else if (__rand__ < .6) { return 2 }
        else { return 3 }
      }
      // var randomI = Math.floor(4 * updateRandom());
      var randomI = __gift__();
      if (ads == true) {
        num++;
        $(".couten").append("<li class='li" + num + "'><img src='" + imgSrc[randomI] + "'><span></span></li>");
        $(".li" + num).css({
          "left": Left,
        });
        $(".li" + num + " a img").css({
          "width": '4rem',
          "transform": "rotate(" + rot + ")",
          "-webkit-transform": "rotate(" + rot + ")",
          "-ms-transform": "rotate(" + rot + ")",
          /* Internet Explorer */
          "-moz-transform": "rotate(" + rot + ")",
          /* Firefox */
          "-webkit-transform": "rotate(" + rot + ")",
          /* Safari  Chrome */
          "-o-transform": "rotate(" + rot + ")" /* Opera */
        });
        $(".li" + num).animate({
          'top': $(window).height() + 20
        }, 5000, function () {
          //
          this.remove()
        });
      }
      add_bag = setTimeout(add, 400)
    }
    var that = this;
    function add_red() {
      num = 0;
      ads = true;
      setTimeout(add, 0);
      setTimeout(function () {
        that.getAd();
        $(".couten").empty();
        clearTimeout(add_bag);
        flag = true;
      }, 5000)
    }
    add_red();
    var flag = true;
    $('.couten').on('click', 'li', function () {
      var _this = $(this);
      _this.find("img").siblings().show();
      setTimeout(function () {
        _this.find("img").siblings().hide();
      }, 500);
      $('.' + $(this).attr('class') + ' img').attr('src', 'https://hdggcdn.bayimob.com/hdggstatic/redBag/baoe.png').css('width', '4rem');
      if (!flag) {
        return
      }
      flag = false;
    });
  }

  // 
  getAd() {
    Request.getAd({
      appkey: this.options.appkey,
      adSpaceKey: this.options.adSpaceKey,
      uid: this.options.uid,
      hdggReferer: utils.gamePath,
      mobileSystem: utils.mobileSystem,
      browserType: utils.browserType,
      advLayout: this.options.advLayout,
      adTriggerType: 'plugInRain',
      gamehtmlId: this.gameHtmlId
    }).then((ad) => {
      $('.hongbaoyu').remove();
      console.log('', ad)
      if (ad.advLayout == 6) {
        // if (this.params.haveDouble) {
        //   let status = "true";
        //   this.changeDoubleStatus(status);
        // };
        let strs = '<div class="propur2"><div class="tcAnimation"><div class="imgBox"></div><div class="linGo"></div></div></div>'
        $('body').append(strs);
        setTimeout(() => {
          $('.jinBox,.redBox').show()
        }, 500)
        setTimeout(() => {
          $('.propur2').append('<span id="closes" class="close-btns closetc iconfont"></span>');
          this.closeClick()
        }, 1500)
      }

      if (!ad.advLayout) {
        console.log(this.options.handleWinClose, '')
        new Noaduc({
          appkey: this.options.appkey,
          closeRed: true,
          myClass: 'mengceng'
        })
        return
      }
      this.showClick(ad)
      this.clickDrop(ad, {
        appkey: this.options.appkey,
        adSpaceKey: this.options.adSpaceKey,
        uid: this.options.uid,
        hdggReferer: utils.gamePath,
        mobileSystem: utils.mobileSystem,
        browserType: utils.browserType,
        advLayout: this.options.advLayout,
        adTriggerType: 'plugInRain',
        gameHtmlId: this.gameHtmlId
      })
    }).catch((err) => {
      console.log(err)
      new Noaduc({
        appkey: this.options.appkey,
        closeRed: true,
        myClass: 'mengceng',
      })
    });
  };
  //
  showGuide() {
    $('.hongbaoyu').show();
    let _this = this
    $('.guide_btn').on('click', function (e) {
      e.preventDefault();
      e.stopPropagation();
      $('.guide_main').hide();
      _this.initRedBao();
    })
    $('#closeRedBag').on('click', function (e) {
      e.preventDefault();
      e.stopPropagation();
      $('.hongbaoyu').remove();
      return
    })
  }
  //
  closeClick() {
    // let _this = this
    $('#closes').on('click', function () {
      // utils.storage.remove('slmShow')
      // _this.close({ ad: this.ad })
      $('.mengceng,.propur2').remove()
    });

  };

  //
  handClick(ad) {
    Request.click({
      statistics: {
        appkey: this.options.appkey,
        adSpaceKey: this.options.adSpaceKey,
        hdggAdvInfoId: ad.id,
        gameName: this.options.gameName,
        uid: this.options.uid,
        sessionId: this.options.sessionId,
        // issuedTime: ad.issuedTime,
        issuedTime: new Date().getTime(),
        province: this.options.province,
        advMaterialId: ad.advMaterialId,
        mobileSystem: utils.mobileSystem,
        imei: this.options.imei,
        doumobkey: ad.doumobkey,
        dkey: ad.dkey,
        type: ad.type,
        // ad: ad,
        bidReqId: ad.bidReqId,
        adPlatformValue: ad.adPlatformValue,
        hdggReferer: utils.gamePath,
        sessId: ad.sessId,
        repeatIssued: ad.repeatIssued,
        adTriggerType: 'plugInRain',
        abGroupId: ad.abGroupId || '',
        abGroupName: ad.abGroupName || '',
        gameHtmlId: this.gameHtmlId,
        mediaMaterialId: utils.getUrlParam('mediaMaterialId') ? utils.getUrlParam('mediaMaterialId') : '',
        clickRate: ad.clickRate,
        ctrType: ad.ctrType
      },
      // dspAd: this.ad.dspAd
    }).then(_ => {
      console.log(this.options)
      this.options.routeToAd({ ad })
    })
    return
  };

  // 
  showClick(ad) {
    Request.show({
      statistics: {
        appkey: this.options.appkey,
        adSpaceKey: this.options.adSpaceKey,
        hdggAdvInfoId: ad.id,
        gameName: this.options.gameName,
        uid: this.options.uid,
        sessionId: this.options.sessionId,
        // issuedTime: ad.issuedTime,
        issuedTime: new Date().getTime(),
        province: this.options.province,
        advMaterialId: ad.advMaterialId,
        mobileSystem: utils.mobileSystem,
        imei: this.options.imei,
        doumobkey: ad.doumobkey,
        dkey: ad.dkey,
        type: ad.type,
        // ad: ad,
        bidReqId: ad.bidReqId,
        adPlatformValue: ad.adPlatformValue,
        hdggReferer: utils.gamePath,
        sessId: ad.sessId,
        repeatIssued: ad.repeatIssued,
        adTriggerType: 'plugInRain',
        abGroupId: ad.abGroupId || '',
        abGroupName: ad.abGroupName || '',
        gameHtmlId: this.gameHtmlId,
        mediaMaterialId: utils.getUrlParam('mediaMaterialId') ? utils.getUrlParam('mediaMaterialId') : '',
        clickRate: ad.clickRate,
        ctrType: ad.ctrType,
        impression_id: utils.getUrlParam('impression_id') ? utils.getUrlParam('impression_id') : '',
        landPageId: ad.landPageId || '',
        landPageChannel: ad.landPageChannel || '',
        linkUrl: ad.urlPath || ''
      },
      // dspAd: this.ad.dspAd

    }).then(_ => {
      console.log('');
      // this.clickAddBi();

      // Utils.copyUrl2(this.ad.dkey)


    })
  };

  // 
  clickDrop(ad, param) {
    $('.linGo,.imgBox').on('click', () => {
      console.log(ad, 'ad')
      // 
      this.handClick(ad)
      // window.location.href = ad.urlPath+'?appkey='+param.appkey+'&adSpaceKey='+param.adSpaceKey+'&uid='+param.uid+'&dkey='+ad.dkey
    })
    $('.tcAnimation').on('click', 'img', () => {
      this.handClick(ad)
      // window.location.href = ad.urlPath+'?appkey='+param.appkey+'&adSpaceKey='+param.adSpaceKey+'&uid='+param.uid+'&dkey='+ad.dkey
    })
  };

  // 
  changeDoubleStatus(status) {
    let gameArr = this.gameArr;
    for (let i in gameArr) {

      if (this.triggerExt == "first" && gameArr[i].type == this.params.type && (gameArr[i].double == null || gameArr[i].double == "true")) {
        gameArr[i].double = status;
        this.currentState.double = status;
        utils.storage.set("gameArr", gameArr);
        utils.storage.set("currentState", this.currentState);
        return;
      }

      if (this.triggerExt == "every" && gameArr[i].type == this.params.type) {
        gameArr[i].double = status;
        this.currentState.double = status;
        utils.storage.set("gameArr", gameArr);
        utils.storage.set("currentState", this.currentState);
        return;
      }

    }

  };
}