import './style/redbagPlug.scss';
import Request from '../core/request';
import utils from '../utils/utils'
import PrizePDD from '../custom/prizePdd';
import Noaduc from './noAdUc'
import { hdggMaterial, hdggMaterial1 } from '../popup/hdggMaterial2';
import { hdggMaterialFlod, hdggMaterialGuaka } from '../popup/materialNteraction';
import SVGA from "svgaplayerweb";

const objDake = {
    hdggMaterial,
    hdggMaterial1,
    hdggMaterialFlod,
    hdggMaterialGuaka
}

export default class redBag {
    constructor(options) {
        this.options = options.options;
        console.log(this.options, '')
        this.create();
        this.num = 0;
        this.add_bag = ''
        this.ads = true;
        this.suiArr = null;
        this.setTime = null;
        this.currentState = utils.storage.get("currentState") || {};
        this.triggerExt = options.options.triggerExt;
        this.params = options.options.options;
        this.gameArr = utils.storage.get('gameArr') || [];
        this.click = options.options.click,
            this.close = options.options.close,
            this.gameHtmlId = options.options.gameHtmlId
    }

    // html
    create() {
        let html = `
            <div no-heatmap class="mengceng">
                <div class="tanchuang">
                        
                </div>
                <div id="hdgg_redbag_rain" style="width: 100%; height: 100%; position: fixed; top: 0; left: 0"></div>
                <!-- <div class="redTitle"></div> -->
                <div class="jishi"></div>
                <ul class="couten">
                
                </ul>
                <div class="timeShow">
                    
                    <span class="daoNum">5</span><span>s</span>
                </div>
                <!-- <div class="topBaoxiang"><span>0</span></div> -->
                <div class="fotterLight">
                    <div class="smallLight">
                        <div class="bigBao"></div>
                    </div>
                </div>
                <div class="timeEnd"></div>
            </div>`;

        $('body').append(html);
        this.initSvgaPlay();
    };

    // svga
    initSvgaPlay() {
        let player = new SVGA.Player("#hdgg_redbag_rain");
        let parser = new SVGA.Parser("#hdgg_redbag_rain");
        let url =
            "https://hdggcdn.bayimob.com/hdggstatic/lsgimg/hdgg_redbag_rain.svga";
        player.loops = "1";
        player.fillMode = "Forward";
        player.clearsAfterStop = false;
        parser.load(url, function (videoItem) {
            player.setVideoItem(videoItem);
            player.startAnimation();
            player.setContentMode("AspectFill");
        });
        player.onFinished(() => {
            this.getAd();
            player.clear();
        });
    };

    // 
    getAd() {
        // $('.timeEnd').show();
        // setTimeout(() => {
        //     $('.timeEnd').hide()
        //     $('.fotterLight').show()
        // }, 1000);

        // $('.bigBao').css({
        //     'background': 'url(https://hdggcdn.bayimob.com/hdggstatic/redBag/baoBg.png) no-repeat',
        //     'background-size': '100%'
        // });

        Request.getAd({
            appkey: this.options.appkey,
            adSpaceKey: this.options.adSpaceKey,
            uid: this.options.uid,
            hdggReferer: utils.gamePath,
            mobileSystem: utils.mobileSystem,
            browserType: utils.browserType,
            advLayout: this.options.advLayout,
            adTriggerType: 'rainEnvelope',
            gamehtmlId: this.gameHtmlId
        }).then((ad) => {
            console.log(ad, 'ad')
            if (ad.advLayout == 1) {
                if (this.params.haveDouble) {
                    let status = "true";
                    this.changeDoubleStatus(status);
                };
                let strs = '<div class="propur"><div class="tcAnimation"><div class="imgBox"></div><div class="imgUrl"><img src="' + ad.imageUrl + '"/></div><div class="txt">' + ad.advIntro + '</div><div class="redBox"></div><div class="jinBox"> </div><div class="baoBox"></div><div class="linGo"></div></div></div>'
                $('body').append(strs);
                setTimeout(() => {

                    $('.jinBox,.redBox').show()
                }, 500)
                setTimeout(() => {
                    $('.propur').append('<span id="closes" class="close-btns closetc iconfont"></span>');
                    this.closeClick()
                }, 1500)

            }
            if (ad.advLayout == 2) {
                if (this.params.haveDouble) {
                    let status = "true";
                    this.changeDoubleStatus(status);
                };
                let params = {
                    ad,
                    closeRed: true,
                    myClass: 'mengceng',
                    myClick: this.handClick,
                    appkey: this.options.appkey,
                    adSpaceKey: this.options.adSpaceKey,
                    hdggAdvInfoId: ad.id,
                    gameName: this.options.gameName,
                    uid: this.options.uid,
                    sessionId: this.options.sessionId,
                    // issuedTime: ad.issuedTime,
                    issuedTime: new Date().getTime(),
                    province: this.options.province,
                    advMaterialId: ad.advMaterialId,
                    mobileSystem: utils.mobileSystem,
                    imei: this.options.imei,
                    doumobkey: ad.doumobkey,
                    dkey: ad.dkey,
                    type: ad.type,
                    // ad: ad,
                    bidReqId: ad.bidReqId,
                    adPlatformValue: ad.adPlatformValue,
                    hdggReferer: utils.gamePath,
                    sessId: ad.sessId,
                    repeatIssued: ad.repeatIssued,
                    adTriggerType: 'rainEnvelope',

                    gameHtmlId: this.gameHtmlId,
                    routeToAd: this.options.routeToAd,
                    screenWide: window.screen.width,
                    screenHigh: window.screen.height,
                    screenZoom: 3

                }
                // this.click({ad:ad})
                console.log(ad.materialImageList, 'vfggrthggn')
                if (ad.materialImageList && ad.materialImageList.length == 2) {
                    let options = {
                        ad: ad,
                        click: this.click,
                        close: this.close,
                        appkey: this.options.appkey,
                        adSpaceKey: this.options.adSpaceKey,
                        gameName: this.options.gameName,
                        uid: this.options.uid,
                        sessionId: this.options.sessionId,
                        imei: this.options.imei,
                        province: this.options.province,
                        redChajian: '',
                        gameHtmlId: this.gameHtmlId
                    }
                    new objDake.hdggMaterial({
                        options: options,
                        routeToAd: options.routeToAd
                    });
                    $('.mengceng,.propur').remove()
                    return
                }
                else if (ad.materialImageList && ad.materialImageList.length == 3) {
                    let options = {
                        ad: ad,
                        click: this.click,
                        close: this.close,
                        appkey: this.options.appkey,
                        adSpaceKey: this.options.adSpaceKey,
                        gameName: this.options.gameName,
                        uid: this.options.uid,
                        sessionId: this.options.sessionId,
                        imei: this.options.imei,
                        province: this.options.province,
                        redChajian: '',
                        gameHtmlId: this.gameHtmlId
                    }
                    new objDake.hdggMaterial1({
                        options: options,
                        routeToAd: options.routeToAd
                    });
                    $('.mengceng,.propur').remove()
                    return
                } else {
                    new PrizePDD(params)
                }

            }

            if (ad.advLayout == 5) {

                let options = {
                    ad: ad,
                    click: this.click,
                    close: this.close,
                    appkey: this.options.appkey,
                    adSpaceKey: this.options.adSpaceKey,
                    gameName: this.options.gameName,
                    uid: this.options.uid,
                    sessionId: this.options.sessionId,
                    imei: this.options.imei,
                    province: this.options.province,
                    redChajian: '',
                    gameHtmlId: this.gameHtmlId
                }

                $('.mengceng,.propur').remove()

                if (ad.interactiveMaterial.interactiveType == 'FLOP') {
                    new objDake.hdggMaterialFlod({
                        options: options,
                        // routeToAd:options.routeToAd,

                    })
                    return
                }
                if (ad.interactiveMaterial.interactiveType == 'SHAVE_CARD') {
                    utils.session.set('guaka_drop', true)
                    new objDake.hdggMaterialGuaka({
                        options: options,
                        // routeToAd:options.routeToAd,

                    })
                    return
                }
            }
            if (!ad.advLayout) {

                console.log(this.options.handleWinClose, '')

                new Noaduc({
                    appkey: this.options.appkey,
                    closeRed: true,
                    myClass: 'mengceng'
                })
            }
            this.showClick(ad)
            this.clickDrop(ad, {
                appkey: this.options.appkey,
                adSpaceKey: this.options.adSpaceKey,
                uid: this.options.uid,
                hdggReferer: utils.gamePath,
                mobileSystem: utils.mobileSystem,
                browserType: utils.browserType,
                advLayout: this.options.advLayout,
                adTriggerType: 'rainEnvelope',
                gameHtmlId: this.gameHtmlId
            })
        }).catch((err) => {
            console.log(err)
            new Noaduc({
                appkey: this.options.appkey,
                closeRed: true,
                myClass: 'mengceng',

            })
        });
    };

    //
    closeClick() {
        $('#closes').on('click', function () {
            utils.storage.remove('slmShow')
            $('.mengceng,.propur').remove()
        });

    };

    //
    handClick(ad) {
        Request.click({
            statistics: {
                appkey: this.options.appkey,
                adSpaceKey: this.options.adSpaceKey,
                hdggAdvInfoId: ad.id,
                gameName: this.options.gameName,
                uid: this.options.uid,
                sessionId: this.options.sessionId,
                // issuedTime: ad.issuedTime,
                issuedTime: new Date().getTime(),
                province: this.options.province,
                advMaterialId: ad.advMaterialId,
                mobileSystem: utils.mobileSystem,
                imei: this.options.imei,
                doumobkey: ad.doumobkey,
                dkey: ad.dkey,
                type: ad.type,
                // ad: ad,
                bidReqId: ad.bidReqId,
                adPlatformValue: ad.adPlatformValue,
                hdggReferer: utils.gamePath,
                sessId: ad.sessId,
                repeatIssued: ad.repeatIssued,
                adTriggerType: 'rainEnvelope',
                abGroupId: ad.abGroupId || '',
                abGroupName: ad.abGroupName || '',
                gameHtmlId: this.gameHtmlId,
                mediaMaterialId: utils.getUrlParam('mediaMaterialId') ? utils.getUrlParam('mediaMaterialId') : '',
                clickRate: ad.clickRate,
                ctrType: ad.ctrType
            },
            // dspAd: this.ad.dspAd
        }).then(_ => {
            console.log(this.options)
            this.options.routeToAd({ ad })
        })
        return
    };

    // 
    showClick(ad) {
        Request.show({
            statistics: {
                appkey: this.options.appkey,
                adSpaceKey: this.options.adSpaceKey,
                hdggAdvInfoId: ad.id,
                gameName: this.options.gameName,
                uid: this.options.uid,
                sessionId: this.options.sessionId,
                // issuedTime: ad.issuedTime,
                issuedTime: new Date().getTime(),
                province: this.options.province,
                advMaterialId: ad.advMaterialId,
                mobileSystem: utils.mobileSystem,
                imei: this.options.imei,
                doumobkey: ad.doumobkey,
                dkey: ad.dkey,
                type: ad.type,
                // ad: ad,
                bidReqId: ad.bidReqId,
                adPlatformValue: ad.adPlatformValue,
                hdggReferer: utils.gamePath,
                sessId: ad.sessId,
                repeatIssued: ad.repeatIssued,
                adTriggerType: 'rainEnvelope',
                abGroupId: ad.abGroupId || '',
                abGroupName: ad.abGroupName || '',
                gameHtmlId: this.gameHtmlId,
                mediaMaterialId: utils.getUrlParam('mediaMaterialId') ? utils.getUrlParam('mediaMaterialId') : '',
                clickRate: ad.clickRate,
                ctrType: ad.ctrType,
                impression_id: utils.getUrlParam('impression_id') ? utils.getUrlParam('impression_id') : '',
                landPageId: ad.landPageId || '',
                landPageChannel: ad.landPageChannel || '',
                linkUrl: ad.urlPath || ''
            },
            // dspAd: this.ad.dspAd

        }).then(_ => {
            console.log('');
            // this.clickAddBi();

            // Utils.copyUrl2(this.ad.dkey)


        })
    };

    // 
    clickDrop(ad, param) {
        $('.linGo').on('click', () => {
            console.log(ad, 'ad')
            // 
            this.handClick(ad)
            // window.location.href = ad.urlPath+'?appkey='+param.appkey+'&adSpaceKey='+param.adSpaceKey+'&uid='+param.uid+'&dkey='+ad.dkey
        })
        $('.tcAnimation').on('click', 'img', () => {
            this.handClick(ad)
            // window.location.href = ad.urlPath+'?appkey='+param.appkey+'&adSpaceKey='+param.adSpaceKey+'&uid='+param.uid+'&dkey='+ad.dkey
        })
    };

    // 
    changeDoubleStatus(status) {
        let gameArr = this.gameArr;
        for (let i in gameArr) {

            if (this.triggerExt == "first" && gameArr[i].type == this.params.type && (gameArr[i].double == null || gameArr[i].double == "true")) {
                gameArr[i].double = status;
                this.currentState.double = status;
                utils.storage.set("gameArr", gameArr);
                utils.storage.set("currentState", this.currentState);
                return;
            }

            if (this.triggerExt == "every" && gameArr[i].type == this.params.type) {
                gameArr[i].double = status;
                this.currentState.double = status;
                utils.storage.set("gameArr", gameArr);
                utils.storage.set("currentState", this.currentState);
                return;
            }

        }

    };
}