import './style/noAdUc.scss'
import Request from '../core/request' 
import Utils from '../utils/utils'
// import utils from '../../interaction/utils';
export default class noAdUc{
  constructor(options) {
	  console.log(options,'43454545')
    this.close = options.close;
		this.appkey = options.appkey;
		this.adSpaceKey = options.adSpaceKey
		this.closeRed = options.closeRed;
		this.myClass = options.myClass;
		// this.isDouble11 = options.isDouble11
    this.create();
    this.handleClick();
  }

  create() {
	//
	let num = 2
		console.log(this.appkey + 'options')
		if(this.appkey=='4a708280788589a5e04626da33190567'||this.appkey=='b84c22d4ac4cdb805a7648ccd0282796'|| this.appkey=='0fc436e928caa10d823dabfca0ab1abf'||this.appkey=='c23d48c36bb99f3c9598c65daf745c78'||this.appkey=='c37ec21c6ee87e1a183bfde8ee96583b'||this.appkey=='c37ec21c6ee87e1a183bfde8ee96583b') {
			if($('#baiduBox').length > 0) {
			      $('#baiduBox').remove();
			    }
			   
					let script = document.createElement('script');
					script.src = 'https://cpro.baidustatic.com/cpro/ui/cm.js';
					var d = document.getElementsByTagName("script")[0];
					  d.parentNode.insertBefore(script, d);
					(function() {
						var s = "_" + Math.random().toString(36).slice(2);
						$('body').append('<div id="baiduBox"><div class="bgBox"><div class="imgBox"><div style="" id="'+s+'"></div></div></div><div class="closeBtn"></div></div>');
						(window.slotbydup = window.slotbydup || []).push({
							id: "u3783465",
							container:  s
						});
					})();
			
			//     setTimeout(function() {
			//       $('#hdgg_show-noadUc .m-wrapper').addClass('hdgg_result_show_animation').css('opacity', 1);
			//     }, 0)
			    setTimeout(function() {
			      $('#baiduBox .closeBtn').show();
			    }, 600)
		}else {
			if($('#hdgg_show-noadUc').length > 0) {
				$('#hdgg_show-noadUc').remove();
			  }
			   
			  $('body').append(`
				<div no-heatmap id="hdgg_show-noadUc" class="m-hdgg-no-ad">
				  <div class="m-close" style="display: none;"></div>
				  <div class="m-wrapper" style="opacity: 1;">
					<div class="m-box">
					  <div id="hdgg_click_ad_btn" class="m-btn">${__("")}</div>
					</div>
				  </div>
				</div> 
			  `)
		      let param = {
				event: "list_show",
				appkey: this.appkey,
				adSpaceKey: this.adSpaceKey,
				uid: Utils.cookies('hdgg_uid'),
				gamehtmlId:Utils.getUrlParam('hdgghtmlid')||'',
				expansion:{
					activityId: "translate",
				  }
			  }
			  this.gameListShow (param)  
			  setTimeout(function() {
				$('#hdgg_show-noadUc .m-wrapper').addClass('hdgg_result_show_animation').css('opacity', 1);
			  }, 0)
			  setTimeout(function() {
				$('#hdgg_show-noadUc .m-close').show();
			  }, 600)
		}
     
  }

  handleClick() {
    $('#baiduBox .closeBtn').on('click', ()=> {
      $(this).hide();
	 
	  
	  this.close();
      // $('#hdgg_show-noadUc .m-wrapper').addClass('hdgg_result_hide_animation').css('opacity', 0);
      setTimeout(function() {
        $('#baiduBox').remove();

        if (!!window.styleReset && typeof window.styleReset == 'function') {
          try {
            window.styleReset();
          }catch(err){}
        }
      }, 400)
	})
		

		$('#hdgg_show-noadUc .m-close, #hdgg_click_ad_btn').on('click', ()=> {
			  $(this).hide();
			  if(this.closeRed) {
				   
				  console.log($('.'+this.myClass))
				$('.'+this.myClass).remove()
				$('#hdgg_show-noadUc').remove();
				return
			   }
		      this.close();
		      $('#hdgg_show-noadUc .m-wrapper').addClass('hdgg_result_hide_animation').css('opacity', 0);
		      setTimeout(function() {
		        $('#hdgg_show-noadUc').remove();
		
		        if (!!window.styleReset && typeof window.styleReset == 'function') {
		          try {
		            window.styleReset();
		          }catch(err){}
		        }
		      }, 400)
		    })
  }
  gameListShow (param) {

    Request.gameListClick(param).then((res) => {
      // console.log(newurl)

      // window.location.href = dropUrl;

      console.log(res + '')
    }).catch((err) => {
      console.log(err + '')
    })
  }

}