export class hdggMaterial {
  constructor(options) {
    this.options = options.options;
    console.log(options.options,'wwewghti')
    this.ad = options.options.ad;
    this.click = options.options.click;
    this.close = options.options.close;
    this.create();
    
  }

  create() {
    let material = `
        <div no-heatmap id="hdgg_material_wrappers">
            <div no-heatmap class="hdgg_material_close"></div>
        </div>`;
    $("body").append(material);
    this.bindClose();
      let item = `
          <div class="hdgg_material_card">
              <div class="hdgg_material_card_static hdgg_material_before">
                  <div id="hdgg_material_card_before"></div>
              </div>
              <div class="hdgg_material_card_static hdgg_material_click">
                  <div id="hdgg_material_card_click"></div>
              </div>
              
               
          </div>`;
      $("#hdgg_material_wrappers").prepend(item);
      this.render_card();
  };
  bindClose() {
    $("#hdgg_material_wrappers").on("click", ".hdgg_material_close", () => {
      this.close({ ad: this.ad })
      $("#hdgg_material_wrappers").remove();
      if(this.ad.adTriggerType!="doublePop") {
        if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
          try {
            window.fragmentShow()
          } catch (err) {
  
          }
        }
      }
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) { }
      }
    });
  };
  render_card() {
    let _this = this;
    var player = new SVGA.Player('#hdgg_material_card_before');
    var parser = new SVGA.Parser('#hdgg_material_card_before'); //  IE6+ Parser
    parser.load(this.options.ad.materialImageList[0].imageUrl, function (videoItem) {
      player.setVideoItem(videoItem);
      player.startAnimation();
      try {
        if(_this.options.redChajian) {
          _this.fetch_show()
        } 
      }catch(err) {
         console.log(err,'')
      }
    });

    var playerClick = new SVGA.Player('#hdgg_material_card_click');
    var parserClick = new SVGA.Parser('#hdgg_material_card_click');
    parserClick.load(this.options.ad.materialImageList[1].imageUrl, function (videoItem) {
      $("#hdgg_material_wrappers").one("click", "#hdgg_material_card_before canvas", () => {
        $(".hdgg_material_click").show();
        $(".hdgg_material_before").remove();
        $(".hdgg_material_close").hide();
        playerClick.setVideoItem(videoItem);
        playerClick.startAnimation();
        // setTimeout(() => {
          _this.routeToAd();
        // }, 3500);
      })
    });
    playerClick.loops = "1";
    playerClick.onFinished(function () {
      // $(".hdgg_material_click").remove();
      // $(".hdgg_material_show").show()
    });
  };
  routeToAd() {
    let _this = this;
    setTimeout(() => {
      _this.click({
        ad: _this.ad,
      });
    }, 1000);
    setTimeout( () => {
      $("#hdgg_material_wrappers").remove();
      if(this.ad.adTriggerType!="doublePop") {
        if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
          try {
            window.fragmentShow()
          } catch (err) {
  
          }
        }
      }
      
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) { }
      }
    },2000);
  }
  fetch_show() {
    Request.show({

      statistics: {
        appkey: this.options.appkey,
        adSpaceKey: this.options.adSpaceKey,
        hdggAdvInfoId: this.ad.id,
        gameName: this.options.gameName,
        uid: this.options.uid,
        sessionId: this.options.sessionId,
        advMaterialId: this.ad.advMaterialId,
        mobileSystem: Utils.mobileSystem,
        province: this.options.province,
        imei: this.options.imei,
        // type: this.ad.type,
        bidReqId: this.ad.bidReqId,
        adPlatformValue: this.ad.adPlatformValue,
        hdggReferer: Utils.gamePath,
        sessId: this.ad.sessId,
        repeatIssued: this.ad.repeatIssued,
        adTriggerType: this.ad.adTriggerType,
        gameHtmlId: this.ad.gameHtmlId || '',
        abGroupId: this.ad.abGroupId || '',
        abGroupName: this.ad.abGroupName || '',
        mediaMaterialId: Utils.getUrlParam('mediaMaterialId') ? Utils.getUrlParam('mediaMaterialId') : '',
        clickRate: this.ad.clickRate,
        ctrType: this.ad.ctrType
      },
      dspAd: this.ad.dspAd

    })
  }
}
