import './style/record.scss'
import Utils from '../utils/utils'
import Request from '../core/request'
import { CustomPrize } from './customPrize'
import SVGA from 'svgaplayerweb';
export class RecordButton {
    constructor(options) {
        this.options = options.options;

        console.log(this.options, 'options')
    }

    create() {
        //
        return new Promise((resolve, reject) => {
            $('body').append(`
        <div id="hdgg_record_button">
          <div class="m-inner">
            <div class="m-wrapper"></div>
            <div class="m-sj"></div>
            <p class="m-text"></p>
          </div>
        </div>
      `)
            if (Object.keys(this.options.recordStyle).length > 0) {
                $("#hdgg_record_button").css(this.options.recordStyle)
            }
            $('#hdgg_record_button .m-inner').css('background', this.options.recordButtonColor[0]);
            $('#hdgg_record_button .m-inner .m-wrapper').css('background', this.options.recordButtonColor[1]);

            console.log('');
            resolve($('#hdgg_record_button .m-inner'));
        })
    }
}



export class RecordList {
    constructor(options) {
        this.options = options.options;
        this.record = options.record;
        this.appkey = options.appkey;
        this.adSpaceKey = options.adSpaceKey;
        this.uid = options.uid;
        // this.sessId = options.sessId,
        // this.repeatIssued = options.repeatIssued
        this.create();
        this.render();
        this.styleBugFix();
        this.createPhone();
    }

    create() {
        console.log('', this.options)
        if ($("#hdgg-record-page").length > 0) {
            return;
        }
        $('body').append(`
      <div no-heatmap id="hdgg-record-page" style="display: none;">
        <div class="hd_ad_hd">
          <div class="hd_ad_back">${__("")}</div>
          <p>${__("")}</p>
        </div>
        <div id="someMedia" style="overflow:auto">
        <div class="hd_ad_list">
          
        </div>
      </div>
    `)
        if (this.options.appkey == 'ffb24d8f20df8e25c9bcb8fe58742894' || this.options.appkey == 'be38f8ab6179abd5954573dcdd16d86b1' ||
            this.options.appkey == '9273f5d67ed1eeeec916231d1987d28f' || this.options.someMedia) {
            $(` <div class="hd_ad_mobile">
        <p><a href="tel://010-56128391" style="color:#fff;">010-56128391</a></p>
        </div>`).insertBefore($('.hd_ad_list'));
        }
        if (this.options.recordAnimate) {
            $('#hdgg-record-page').addClass('m-transition-dom')
        }

        console.log('')

        $('#hdgg-record-page .hd_ad_back').on('click', function () {
            //
            window.parent.postMessage({ block: true }, '*')
            $('#hdgg-record-page').removeClass('hdgg-record-show').addClass('hdgg-record-hide')
            setTimeout(function () {
                $('#hdgg-record-page').removeClass('hdgg-record-hide').css('display', 'none')
            }, 800)
        })
    }

    render() {
        console.log('', this.record);
        Request.showRecord({
            appkey: this.appkey,
            adSpaceKey: this.adSpaceKey,
            uid: this.uid
        })
        $('#hdgg-record-page .hd_ad_list a').remove();
        $('#hdgg-record-page .hd_ad_noprize').remove();

        if (this.record.length == 0 && $('#hdgg-record-page .hd_ad_noprize').length == 0) {
            $('#hdgg-record-page .hd_ad_list').append(`
        <div class="hd_ad_noprize">${__(",")}</div>
      `)
            return;
        }
        let currentIndex = 0;
        this.record.forEach((n) => {
            let url = n.fullpath || n.urlPath;
            if (n.imageUrl.indexOf('.svga') !== -1) {
                currentIndex++;
                $('#hdgg-record-page .hd_ad_list').append(`
                    <a href="javascript:;" data-url="${url}" data-doumobkey="${n.doumobkey}" data-receiveid="${n.receiveId}" data-type="${n.type}">
                    <div id="${'hdggImage' + currentIndex}" class="tip" style="width:4.75rem;height:4.75rem;overflow: hidden;margin-right: 0.6rem;float: left;">
                    </div>
                    <p>${n.message || n.advIntro || ""}</p>
                    
                    <div class="hd_ad_lingjiang"></div>
                    </a>`)
                var player = new SVGA.Player('#hdggImage' + currentIndex);
                var parser = new SVGA.Parser('#hdggImage' + currentIndex); //  IE6+ Parser
                parser.load(n.imageUrl, function (videoItem) {

                    player.setVideoItem(videoItem);
                    console.log(player.videoSize, 'svga')
                    player.startAnimation();
                });
                // player.setContentMode("ScaleToFill");
            } else {
                let recordUrl = '';
                if (n.interactiveMaterial) {
                    if (n.interactiveMaterial.interactiveType == 'FLOP') {
                        recordUrl = 'https://cdn.vedioads.cn/' + n.interactiveMaterial.materialSet.prize
                    }
                    if (n.interactiveMaterial.interactiveType == 'SHAVE_CARD') {
                        recordUrl = 'https://cdn.vedioads.cn/' + n.interactiveMaterial.materialSet.back
                    }
                } else {
                    recordUrl = n.imageUrl
                }
                $('#hdgg-record-page .hd_ad_list').append(`
                <a href="javascript:;" data-url="${url}" data-doumobkey="${n.doumobkey}" data-receiveid="${n.receiveId}" data-type="${n.type}">
                    <img src="${recordUrl}"/>
                    <p>${n.message || n.advIntro || ""}</p>
                    <div class="hd_ad_lingjiang"></div>
                  </a>  
                  `)
            }
            // <a href="javascript:;" data-url="${n.urlPath}" data-id="${n.id}" data-type="${n.type}" data-issuedTime="${n.issuedTime}" data-appId="${n.appId}" data-doumobkey=${n.doumobkey}>


        });

        let _this = this;
        $('#hdgg-record-page .hd_ad_list a').on('click', function () {
            // if (window.__wxjs_environment === 'miniprogram' ) {
            //   let appId = $(this).attr('data-appId')
            //   let doumobkey = $(this).attr('data-doumobkey');
            //   that._miniProgram(u, appId, doumobkey);
            //   return;
            // }
            window.localStorage.removeItem("fromVideo");
            let doumobkey = $(this).attr('data-doumobkey');
            let u = $(this).attr('data-url');
            let adsRecord = Utils.storage.get("adsRecord");
            if ($(this).attr('data-receiveid') && $(this).attr('data-type') == "custom") {
                for (let i in adsRecord) {
                    if (adsRecord[i].receiveId == $(this).attr('data-receiveid')) {
                        _this.checkRecordStatus(i, u);
                    }
                }
                return;
            };

            Request.recordClick({
                appkey: _this.appkey,
                adSpaceKey: _this.adSpaceKey,
                uid: _this.uid,
                // sessId: _this.sessId,
                // repeatIssued: _this.repeatIssued,
                doumobkey,
            }).then(_ => {
                if (_this.options.ifranmeShow) {
                    window.parent.postMessage({ doumob: u }, '*')
                } else {
                    window.location.href = u;
                }
            })
        });
    }

    checkRecordStatus(index, u) {
        let _this = this;
        let adsRecord = Utils.storage.get("adsRecord");
        let receiveId = adsRecord[index].receiveId;
        if (adsRecord[index].type == "custom" && adsRecord[index].urlType == "2") {
            new CustomPrize({
                appkey: this.appkey,
                adSpaceKey: this.adSpaceKey,
                uid: this.uid,
                receiveId: receiveId,
                deviceId: Utils.getUrlParam('deviceId') || null,
                userId: Utils.getUrlParam('userId') || null,
                os: Utils.getUrlParam('os') || null,
                appName: Utils.getUrlParam('appName') || null,
            });
            return;
        } else if (adsRecord[index].type == "custom" && adsRecord[index].urlType == "1") {
            Request.getCustomInfo({
                appkey: this.appkey,
                adSpaceKey: this.adSpaceKey,
                uid: this.uid,
                receiveId: receiveId
            }).then((res) => {
                if (_this.options.ifranmeShow) {
                    window.parent.postMessage({ doumob: u }, '*')
                } else {
                    window.location.href = u;
                }
            })
            // .catch((err) => {
            //     window.location.href = u;
            // })
            return;
        };
    }

    styleBugFix() {
        $('#hdgg-record-page').css('display', 'block')
        $('#hdgg-record-page .hd_ad_list')[0].scrollTop = 0
        setTimeout(() => {
            $('#hdgg-record-page').addClass('hdgg-record-show')
        }, 0)
    }

    createPhone() {
        if (this.adSpaceKey == '6453426ec2ce2022b3ffd4764ffb40bc') {
            $('#hdgg-record-page .hd_ad_hd').append(`
        <div class="m-service-phone">010-56128391</div>
      `);
        }
    }
}