import './style/kefu.scss'

export default class Kefu {

	constructor(options) {

		this.options = options;
		console.log(options);
		this.create();
		this.handleClick();
	}

	handleClick() {

		$(".Kefu").on("click", () => {
			$(".pop_kefu").show();
		});
		this.handleClose();
	}

	create() {
		if(this.options.appkey=='b6291609a66c78f174e838ae97672bc8') {
			$('body').append(
				`<div no-heatmap class="Kefu"></div>
				<div no-heatmap class="pop_kefu">
					<div class="kefu_content">
						<div class="kefu_item">
							 <a></a>
						</div>
						<div class="close"></div>
					</div>
				</div>`
			);
			setTimeout(() => {
				$('.pop_kefu .kefu_content').css('height','12.2rem')
			}, 0);
		}else {	
		$('body').append(
			`<div no-heatmap class="Kefu"></div>
			<div no-heatmap class="pop_kefu">
				<div class="kefu_content">
					<div class="kefu_item">
						<a href="https://cschat-ccs.aliyun.com/index.htm?tntInstId=_1VPXvay&scene=SCE00005273"></a>
						<a href="tel:010-85759707"></a>
					</div>
					<div class="close"></div>
				</div>
			</div>`
		);
		}
		let adSpaceKey = this.options.adSpaceKey;
		if (adSpaceKey == "6f0ce7c9203b2bbf94dfa06ae76e3dcd" || adSpaceKey == "71f7c076c3c552d9eb50cf8a2cc61016" || adSpaceKey == "31507b3303ad5f8c434d068dcfd7fe40" || adSpaceKey == "ace6f2c9a07d8b7533955fb280247967") {
			$(".Kefu").addClass("special");
		};

		if (this.options.customer == "2") {
			$(".Kefu").addClass("special");
		}
	}

	handleClose() {

		$(".pop_kefu .close").on("click", () => {
			$(".pop_kefu").hide();
		})
	}

}
