export class hdggMaterialFlod {
  constructor(options) {

    this.options = options.options;

    this.setinterTime = null
    // this.routeToAd = options.routeToAd
    // this.routeToAd = options.routeToAd
    this.ad = options.options.ad
    this.click = options.options.click;
    this.close = options.options.close
    console.log(this.options, 'IDoptions')
    this.create();
    this.render( this.ad, this.click, this.setinterTime);
  }

  create() {

    //
    return new Promise((resolve, reject) => {
      $('body').append(`
             <div class="materialPropu5">
             <div class="material1Guang1"></div>
             <div class="material1Bg1" style="background-image: url(https://cdn.vedioads.cn/${this.ad.interactiveMaterial.materialSet.back})">
             <div id="materialBox2">
             <div class="material1RedBao4 material2" style="background-image: url(https://cdn.vedioads.cn/${this.ad.interactiveMaterial.materialSet.cardBack})"></div>
             <div class="material1RedBao5 material2" style="background-image: url(https://cdn.vedioads.cn/${this.ad.interactiveMaterial.materialSet.cardBack})"></div>
             <div class="material1RedBao6 material2" style="background-image: url(https://cdn.vedioads.cn/${this.ad.interactiveMaterial.materialSet.cardBack})"></div>
             
             </div>
             <div class="material1Go">
             <img src="https://cdn.vedioads.cn/${this.ad.interactiveMaterial.materialSet.guide}"/>
             </div>
             </div>
             <div class="materialClose2"></div>
             </div>
        `)
         setTimeout(()=>{
           $('.materialClose2').show()
         },1000)
        // alert(window.innerHeight)
        // alert(window.innerWidth)
        // if(window.innerHeight/window.innerWidth<1.3) {
        //   $('.materialPropu5 .material1Bg1').css({
        //     width:'70%',
        //     marginLeft:'-35%'
        //   })
        //   $('#materialBox2 .material1RedBao4').css({
        //     width:'4rem'
        //   })
        //   $('#materialBox2 .material1RedBao5').css({
        //     width:'4rem'
        //   })
        //   $('#materialBox2 .material1RedBao6').css({
        //     width:'4rem'
        //   })
        // }
    })

  
  }
  render( ad, click, setinterTime) {
    try {
      if (this.options.redChajian) {
        this.fetch_show()
      }
    } catch (err) {
      console.log(err)
    }
    let _this = this
    var danindex = 0
    this.setinterTime = setInterval(function () {
      // if (dansuiSwitch == true) {
      danindex = danindex == $('#materialBox2').find('.material2').length ? 0 : danindex;
      $('#materialBox2').find('.material2').eq(danindex).addClass('materialBao').siblings('.materialBao').removeClass('materialBao')
      danindex++;
      
      // }  



    }, 800);
    var stop = false;

    $('#materialBox2').on('click', 'div', function () {
      $('#materialBox2').find('.material2').removeClass('materialBao')
      // console.log($(this).attr('id'))
      // clearInterval(setinterTime)
      $('.material1Hand1').hide()

      if (stop) {
        // alert($(this).attr('ad_data'))
        if ($(this).attr('ad_data') == 'stop') {

          _this.click({ 'ad': ad })
        }

        return
      }
      $(this).addClass('pandFan');
      setTimeout(() => {
        $(this).attr('ad_data', 'stop')
        stop = true
        $(this).removeClass('pandFan');
        let winImg = 'https://cdn.vedioads.cn/' + ad.interactiveMaterial.materialSet.prize;
        let filImgm = 'https://cdn.vedioads.cn/' + ad.interactiveMaterial.materialSet.fail;
        var imgArr = [winImg, filImgm];

        $(this).css('background-image', 'url(' + imgArr[0] + ')')

        $('#materialBox2').find('.material2').eq($(this).index()).siblings().addClass('shenFan')

        setTimeout(() => {
          $('#materialBox2').find('.material2').removeClass('shenFan')
          if ($(this).index() == 0) {

            $('#materialBox2').find('.material2').eq($(this).index() + 1).css('background-image', 'url(' + imgArr[1] + ')')
            $('#materialBox2').find('.material2').eq($(this).index() + 2).css('background-image', 'url(' + imgArr[1] + ')')
          }
          if ($(this).index() == 1) {
            $('#materialBox2').find('.material2').eq($(this).index() + 1).css('background-image', 'url(' + imgArr[1] + ')')
            $('#materialBox2').find('.material2').eq($(this).index() - 1).css('background-image', 'url(' + imgArr[1] + ')')
          }
          if ($(this).index() == 2) {
            $('#materialBox2').find('.material2').eq($(this).index() - 2).css('background-image', 'url(' + imgArr[1] + ')')
            $('#materialBox2').find('.material2').eq($(this).index() - 1).css('background-image', 'url(' + imgArr[1] + ')')
          }
          $('#materialBox2').find('.material2').removeClass('materialBao')
          clearInterval(_this.setinterTime)
          $('.material1Go').show()
        }, 450);

        setTimeout(() => {
          click({ 'ad': ad })
          // $('.materialPropu5').remove()
        }, 1000);

        // $(this).css('background-image','url('+imgArr[num]+')')
        //  click({'ad':ad})
        // $('.material1Go').show()
        // setTimeout(() => {
        //     routeToAd({ad})
        // }, 1000);
      }, 450);
    })
    $('.materialClose2').on('click', () => {

      this.close({ ad: this.ad })
      $('.materialPropu5').remove()
      try {
        if(!this.options.redChajian&&this.ad.adTriggerType!="doublePop") {
             
          if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
            try {
              window.fragmentShow()
            } catch (err) {
    
            }
          }
         
        }
      } catch(err) {

      }
      
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) { }
      }
    })
  }
  fetch_show() {
    Request.show({

      statistics: {
        appkey: this.options.appkey,
        adSpaceKey: this.options.adSpaceKey,
        hdggAdvInfoId: this.ad.id,
        gameName: this.options.gameName,
        uid: this.options.uid,
        sessionId: this.options.sessionId,
        advMaterialId: this.ad.advMaterialId,
        mobileSystem: Utils.mobileSystem,
        province: this.options.province,
        imei: this.options.imei,
        // type: this.ad.type,
        bidReqId: this.ad.bidReqId,
        adPlatformValue: this.ad.adPlatformValue,
        hdggReferer: Utils.gamePath,
        sessId: this.ad.sessId,
        repeatIssued: this.ad.repeatIssued,
        adTriggerType: this.ad.adTriggerType,
        gameHtmlId: this.ad.gameHtmlId || '',
        abGroupId: this.ad.abGroupId || '',
        abGroupName: this.ad.abGroupName || '',
        mediaMaterialId: Utils.getUrlParam('mediaMaterialId') ? Utils.getUrlParam('mediaMaterialId') : '',
        clickRate: this.ad.clickRate,
        ctrType: this.ad.ctrType,
        impression_id:Utils.getUrlParam('impression_id')?Utils.getUrlParam('impression_id'):''
      },
      dspAd: this.ad.dspAd

    })
  }
}

