    checkExpireTime() {
        let currentTime = new Date().getTime();
        let date = new Date();
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        let countTimes = year + "/" + month + "/" + day;
        let expireTime = new Date(countTimes).getTime() + 60 * 60 * 24 * 1000;
        if (Utils.storage.get("setTime")) {
            let oldTimes = Utils.storage.get('setTime');
            if (currentTime >= oldTimes) {

                Utils.storage.remove('baoNum')
                Utils.storage.remove('baoAdv')
                Utils.storage.remove('isShowDrop')
                
                Utils.storage.set("setTime", expireTime);
            }
        } else {
            Utils.storage.set("setTime", expireTime);
        }
    }

