export class hdggMaterial5 {
  constructor(options) {

    this.options = options.options;

    this.create();
    // this.render()
    this.setinterTime = null
    this.routeToAd = options.routeToAd
    this.ad = options.options.ad
    this.click = options.options.click;
    this.close = options.options.close
    console.log(options.routeToAd, 'IDoptions')
    this.render(this.routeToAd, this.ad, this.click, this.setinterTime);

  }

  create() {

    //
    return new Promise((resolve, reject) => {
      $('body').append(`
           <div class="materialPropu1">
           <div class="material1Guang"></div>
           <div class="material1Bg">
           <div id="materialBox">
           <div class="material1RedBao1 material"></div>
           <div class="material1RedBao2 material"></div>
           <div class="material1RedBao3 material"></div>
           </div>
           <div class="materialGo"></div>
           </div>
           <div class="material1Hand"></div>
           <div class="materialClose1"></div>
           </div>
      `)

    })


  }
  render(routeToAd, ad, click, setinterTime) {
    var danindex = 0;
    this.setinterTime = setInterval(function () {
      // if (dansuiSwitch == true) {
      danindex = danindex == $('#materialBox').find('.material').length ? 0 : danindex;
      $('#materialBox').find('.material').eq(danindex).addClass('materialBaoGo').siblings('.materialBaoGo').removeClass('materialBaoGo')
      danindex++;

      // }  

    }, 1000);
    $('#materialBox').on('click', 'div', function () {
      console.log(ad.advMaterialId, 'ddvfgfbhgh')
      clearInterval(setinterTime)
      $('#materialBox').find('.material').removeClass('materialBaoGo')
      $(this).addClass('pandFan');
      $('.material1Hand').hide()

      setTimeout(() => {

        $(this).removeClass('pandFan');
        click({ 'ad': ad })
        var imgArr = ['https://hdggcdn.bayimob.com/hdggstatic/materialIng/material1.png', 'https://hdggcdn.bayimob.com/hdggstatic/materialIng/material2.png'];
        // var num = Math.floor(Math.random()*3)
        //    if(ad.advMaterialId==11534) {
        $(this).css('background-image', 'url(https://hdggcdn.bayimob.com/hdggstatic/materialIng/material3.png)')
        $('#materialBox').find('.material').eq($(this).index()).siblings('panFan').addClass('pandFan')
        setTimeout(() => {
          if ($(this).index() == 0) {
            $('#materialBox').find('.material').eq($(this).index() + 1).css('background-image', 'url(' + imgArr[0] + ')')
            $('#materialBox').find('.material').eq($(this).index() + 2).css('background-image', 'url(' + imgArr[1] + ')')
          }
          if ($(this).index() == 1) {
            $('#materialBox').find('.material').eq($(this).index() + 1).css('background-image', 'url(' + imgArr[0] + ')')
            $('#materialBox').find('.material').eq($(this).index() - 1).css('background-image', 'url(' + imgArr[1] + ')')
          }
          if ($(this).index() == 2) {
            $('#materialBox').find('.material').eq($(this).index() - 1).css('background-image', 'url(' + imgArr[0] + ')')
            $('#materialBox').find('.material').eq($(this).index() - 2).css('background-image', 'url(' + imgArr[1] + ')')
          }

          $('.materialGo').show()
          setTimeout(() => {
            routeToAd({ ad })
          }, 1000);
        }, 198);
        //    }

      }, 198);

    })
    $('.materialClose1').on('click', () => {
      this.close({ ad: this.ad })
      $('.materialPropu1').remove()
      if(this.ad.adTriggerType!="doublePop") {
        if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
          try {
            window.fragmentShow()
          } catch (err) {
  
          }
        }
      }
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) { }
      }
    })
  }
}
