export class hdggMaterial7 {
  constructor(options) {
    this.options = options.options;
    this.ad = options.options.ad;
    this.click = options.options.click;
    this.close = options.options.close;
    this.create();
  }

  create() {
    let material = `
        <div no-heatmap id="hdgg_material_wrapper">
            <div no-heatmap class="hdgg_material_close"></div>
        </div>`;
    $("body").append(material);
    this.bindClose();
    if (this.ad.advMaterialId == 120657 || this.ad.advMaterialId == 120427 || this.ad.advMaterialId == 120548 || this.ad.advMaterialId == 124853 || this.ad.advMaterialId == 124856) {
      let src = this.ad.advMaterialId == 120657 ? "https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/hdgg_material_card_after_2.png" : "https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/hdgg_material_card_after_1.png";
      let item = `
          <div class="hdgg_material_card">
              <div class="hdgg_material_card_static hdgg_material_before">
                  <div id="hdgg_material_card_before"></div>
              </div>
              <div class="hdgg_material_card_static hdgg_material_click">
                  <div id="hdgg_material_card_click"></div>
              </div>
              <div class="hdgg_material_card_click_area"></div>
              <div class="hdgg_material_card_static hdgg_material_show">
                  <div class="hdgg_material_card_show">
                      <img src="${ src}" alt="">
                    <div class="hdgg_material_card_button"></div>
                  </div>
              </div>
          </div>`;
      $("#hdgg_material_wrapper").prepend(item);
      this.render_card();
      return;
    }
    if (this.ad.advMaterialId == 120659) {
      let item = `
          <div class="hdgg_material_liquor">
              <div class="hdgg_material_liquor_content">
                  <div class="hdgg_material_liquor_select">
                      <div class="hdgg_material_liquor_mouse hdgg_material_liquor_hand"></div>
                      <div class="hdgg_material_liquor_pig"></div>
                  </div>
              </div>
              <div class="hdgg_material_static">
                  <div id="hdgg_material_liquor_after"></div>
                  <div class="hdgg_material_liquor_btn"></div>
              </div>
          </div>`;
      $("#hdgg_material_wrapper").prepend(item);
      this.render_liquor();
      return;
    }
    if (this.ad.advMaterialId == 120767) {
      let numberArr = [99, 110, 300, 456, 299, 323, 189, 134, 217, 109];
      let number = Utils.storage.get("hdgg_material_item_number") ? parseInt(Utils.storage.get("hdgg_material_item_number")) + 23 : numberArr[Math.floor(Math.random() * 10)];
      number > 999 ? 999 : number;
      Utils.storage.set("hdgg_material_item_number", number);
      let item = `
          <div class="hdgg_material_item">
              <div class="hdgg_material_static hdgg_material_show">
                  <div id="hdgg_material_item_befor"></div>
                  <div class="hdgg_material_item_click"></div>
                  <div class="hdgg_material_item_number">${ number}</div>
              </div>
              <div class="hdgg_material_static hdgg_material_hide">
                  <div id="hdgg_material_item_after"></div>
                  <div class="hdgg_material_item_btn"></div>
              </div>
          </div>`
      $("#hdgg_material_wrapper").prepend(item);
      this.render_item();
    }
  };
  bindClose() {
    $("#hdgg_material_wrapper").on("click", ".hdgg_material_close", () => {
      this.close({ ad: this.ad })
      $("#hdgg_material_wrapper").remove();
      if(this.ad.adTriggerType!="doublePop") {
        if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
          try {
            window.fragmentShow()
          } catch (err) {
  
          }
        }
      }
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) { }
      }
    });
  };
  render_card() {
    let _this = this;
    var player = new SVGA.Player('#hdgg_material_card_before');
    var parser = new SVGA.Parser('#hdgg_material_card_before'); //  IE6+ Parser
    parser.load('https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/hdgg_material_card_before.svga', function (videoItem) {
      player.setVideoItem(videoItem);
      player.startAnimation();
    });

    var playerClick = new SVGA.Player('#hdgg_material_card_click');
    var parserClick = new SVGA.Parser('#hdgg_material_card_click');
    parserClick.load('https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/hdgg_material_card_click.svga', function (videoItem) {
      $("#hdgg_material_wrapper").one("click", ".hdgg_material_card_click_area", () => {
        $(".hdgg_material_click").show();
        $(".hdgg_material_before").remove();
        $(".hdgg_material_close").hide();
        playerClick.setVideoItem(videoItem);
        playerClick.startAnimation();
        setTimeout(() => {
          _this.routeToAd();
        }, 3500);
      })
    });
    playerClick.loops = "1";
    playerClick.onFinished(function () {
      $(".hdgg_material_click").remove();
      $(".hdgg_material_show").show()
    });
  };
  render_liquor() {
    let _this = this;
    var hdgg_material_liquor_interval = setInterval(() => {
      var flag = $("#hdgg_material_wrapper").find(".hdgg_material_liquor_mouse").hasClass("hdgg_material_liquor_hand");
      if (flag) {
        $("#hdgg_material_wrapper").find(".hdgg_material_liquor_mouse").removeClass("hdgg_material_liquor_hand");
        $("#hdgg_material_wrapper").find(".hdgg_material_liquor_pig").addClass("hdgg_material_liquor_hand");
      } else {
        $("#hdgg_material_wrapper").find(".hdgg_material_liquor_mouse").addClass("hdgg_material_liquor_hand");
        $("#hdgg_material_wrapper").find(".hdgg_material_liquor_pig").removeClass("hdgg_material_liquor_hand");
      }
    }, 1500);

    var player_liquor = new SVGA.Player('#hdgg_material_liquor_after');
    var parser_liquor = new SVGA.Parser('#hdgg_material_liquor_after'); //  IE6+ Parser
    parser_liquor.load('https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/liquor_after.svga', function (videoItem) {
      $(".hdgg_material_liquor").on("click", ".hdgg_material_liquor_select div", () => {
        clearInterval(hdgg_material_liquor_interval);
        $(".hdgg_material_liquor_content").hide();
        $(".hdgg_material_close").hide();
        player_liquor.setVideoItem(videoItem);
        player_liquor.startAnimation();
        setTimeout(() => {
          _this.routeToAd();
        }, 300);
      })
    });
  };
  render_item() {
    let numberAdd = [9, 19, 13, 15, 30, 29, 23, 17, 22, 18];
    let number;
    let hdgg_material_item_interval;
    let _this = this;
    var playerItemBefor = new SVGA.Player('#hdgg_material_item_befor');
    var parserItemBefor = new SVGA.Parser('#hdgg_material_item_befor'); //  IE6+ Parser
    parserItemBefor.load('https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/item_before.svga', function (videoItem) {
      playerItemBefor.setVideoItem(videoItem);
      playerItemBefor.startAnimation();
      hdgg_material_item_interval = setInterval(() => {
        number = parseInt($(".hdgg_material_item_number").text()) + numberAdd[Math.floor(Math.random() * 10)];
        if (number >= 999) {
          number = 999;
          clearInterval(hdgg_material_item_interval);
        };
        $("#hdgg_material_wrapper .hdgg_material_item_number").text(number);
      }, 1000);
    });

    var playerItemAfter = new SVGA.Player('#hdgg_material_item_after');
    var parserItemAfter = new SVGA.Parser('#hdgg_material_item_after'); //  IE6+ Parser
    parserItemAfter.load('https://hdggcdn.bayimob.com/hdggstatic/hdgg_material/item_after.svga', function (videoItem) {
      $(".hdgg_material_item").on("click", ".hdgg_material_item_click", () => {
        $(".hdgg_material_show").hide();
        $(".hdgg_material_hide").show();
        $(".hdgg_material_close").hide();
        clearInterval(hdgg_material_item_interval);
        Utils.storage.set("hdgg_material_item_number", number);
        playerItemAfter.setVideoItem(videoItem);
        playerItemAfter.startAnimation();
        setTimeout(() => {
          _this.routeToAd();
        }, 200);
      })
    });
  };
  routeToAd() {
    let _this = this;
    setTimeout(() => {
      _this.click({
        ad: _this.ad,
      });
    }, 1000);
    setTimeout( () => {
      $("#hdgg_material_wrapper").remove();
      if(this.ad.adTriggerType!="doublePop") {
        if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
          try {
            window.fragmentShow()
          } catch (err) {
  
          }
        }
      }
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) { }
      }
    },2000);
  }
}
