import './style/prizePdd.scss'
import utils from '../utils/utils';
import Request from '../core/request'; 
 import  SVGA from 'svgaplayerweb';
// var hop = require('svgaplayerweb/build/svga.createjs.min')
export default class PrizePdd {
  
  constructor(options) {
    console.log('Pdd', options)
    this.ad = options.ad;
    this.click = options.click;
    this.close = options.close;
    this.that = options.that;
    this.closeRed = options.closeRed
    this.myClass = options.myClass;
    this.routeToAd = options.routeToAd
    // this.myClick = options.myClick;
    this.options = options;
    console.log(this.ad)

    this.create();
  }
  
  //   getUrlParam(name) {
  //     let str = 'https://hdggcdn.bayimob.com/hdggstatic/Zujian/ce3.svga?a=1';
  //     console.log(str.substring(1,str.length-1))
	// 	let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
	// 	let r =  str.substr(1).match(reg);
	// 	if (r != null) return decodeURIComponent(r[2]);
	// 	return null;
	// };
  create() {
    if ($('#hdgg_show-win-custom-pdd').length > 0) {
      $('#hdgg_show-win-custom-pdd').remove();
    }
    // console.log(this.getUrlParam('a'),'svga')
    if (this.ad.imageUrl.indexOf('.svga') !== -1) {
      
    //   // let widths = 0;
    //   let height = 0;
    //   if(this.getUrlParam('a')==1) {
    //     height = 12;
    //   }
    //   if(this.getUrlParam('a')==2) {
    //     height  = 20;
    //   }
      $('body').append(`
      <div no-heatmap id="hdgg_show-win-custom-pdd">
      <canvas id="hdgg-image-custom-pdd" style="position: absolute;width:80%;top:6rem;left:1.9rem;" width="750" height="850"></canvas>
      </div>
    `);
     console.log(this.ad.imageUrl,'svga')
    var player = new SVGA.Player('#hdgg-image-custom-pdd');
    var parser = new SVGA.Parser('#hdgg-image-custom-pdd'); //  IE6+ Parser
    parser.load( this.ad.imageUrl, function(videoItem) {
     
      player.setVideoItem(videoItem);
      console.log( player.videoSize,'svga')
      player.startAnimation();
    });
     player.setContentMode("ScaleToFill");
    // player.loops = "1";
    // player.clearsAfterStop = false;
    //  player.fillMode = "Forward";
    
    player.onFinished(()=>{
      // player.clear();
      // $("#svgsCanvas").hide();
      
    });
      
      } else {
      
        $('body').append(`
        <div no-heatmap id="hdgg_show-win-custom-pdd">
          <img id="hdgg-image-custom-pdd" class="m-image" src="${this.ad.imageUrl}">
          <div class="card-sunshine"></div>
        </div>
      `);
      
      }

    
    console.log('')
     


     
    setTimeout(_ => {

      $('#hdgg_show-win-custom-pdd').append('<span class="close-btn closetc iconfont"></span>');

      this.hadnleClose();
    }, 1000)

    this.hadnleClick();
  }

  hadnleClose() {
    $('#hdgg_show-win-custom-pdd .close-btn').on('click', () => {
      $('#hdgg_show-win-custom-pdd').remove();
      if( this.closeRed) {
        $('.'+this.myClass).remove()
        return
      }
      this.ad.adTriggerType != "dropEnvelope" && this.close();
      if(this.ad.adTriggerType!="doublePop") {
        if(!!window.fragmentShow&& typeof window.fragmentShow=='function') {
          try {
            window.fragmentShow()
          } catch (err) {
  
          }
        }
      }
      if (!!window.styleReset && typeof window.styleReset == 'function') {
        try {
          window.styleReset();
        } catch (err) {}
      }
    })
  }

  hadnleClick() {


    $('#hdgg-image-custom-pdd').on('click', (e) => {
      
      e.preventDefault();
      if( this.closeRed) {
        Request.click({
      
          statistics: {
            appkey: this.options.appkey,
            adSpaceKey: this.options.adSpaceKey,
            hdggAdvInfoId: this.ad.id,
            gameName: this.options.gameName,
            uid: this.options.uid,
            sessionId: this.options.sessionId,
            // issuedTime: ad.issuedTime,
            issuedTime: new Date().getTime(),
            province: this.options.province,
            advMaterialId: this.ad.advMaterialId,
            mobileSystem: utils.mobileSystem,
            imei: this.options.imei,
            doumobkey: this.ad.doumobkey,
            dkey: this.ad.dkey,
            type: this.ad.type,
            // ad: ad,
            bidReqId: this.ad.bidReqId,
            adPlatformValue: this.ad.adPlatformValue,
            hdggReferer: utils.gamePath,
            sessId: this.ad.sessId,
            repeatIssued: this.ad.repeatIssued,
            adTriggerType: 'rainEnvelope',
            mediaMaterialId:utils.getUrlParam('mediaMaterialId')?utils.getUrlParam('mediaMaterialId'):'',
            gameHtmlId:this.ad.gameHtmlId
          },
          // dspAd: this.ad.dspAd
    
        }).then(_ => {
          console.log('');
          // this.clickAddBi();
          let ad = this.ad
          // Utils.copyUrl2(this.ad.dkey)
          this.routeToAd({ad})
          // window.location.href = this.ad.urlPath+location.search+'&dkey='+this.ad.dkey+'&doumobkey='+this.ad.doumobkey+'&doumobkey='+this.ad.doumobkey+'&uid='+this.options.uid
          // +'&advMaterialId='+this.ad.advMaterialId+'&sessionId='+this.options.sessionId+'&mobileSystem='+utils.mobileSystem+'&gameName='+this.options.gameName+'&sessId='+
          // this.ad.sessId+'&repeatIssued='+this.ad.repeatIssued+'&hdggReferer='+utils.gamePath+'&province='+this.options.province;
        })
        return
      }
      this.click({
        ad: this.ad,
      });
      // utils.copyUrl2(this.ad.dkey)

      return false;
    })

  }
}