import './style/prize.scss'
import utils from '../utils/utils';

export default class Prize{
  constructor(options) {
    this.ad = options.ad;
    this.click = options.click;
    this.close = options.close;
    this.that = options.that;
    console.log(this.ad)

    this.create();
  }

  create() {
    if($('#hdgg_show-win').length > 0) {
      $('#hdgg_show-win').remove();
    }

    $('body').append(`
      <div no-heatmap id="hdgg_show-win">
        <div class="popShowPrize dialog5" style="display: block; transform-origin: 0px 0px 0px; opacity: 1; transform: scale(1, 1);">
          <div class="card-sunshine"></div>
          <div class="m-box"></div>
          <div class="showPrize-dialog modal-body">
            <div id="hdgg_click_ad_img" class="card-bg" style="background-image: url(https://hdggcdn.bayimob.com/hdggstatic/hongbao/hongcardbg_goldegg.png)">
              <div class="m-image">
                <div class="image" style="background: url(${this.ad.imageUrl}) 0 0 no-repeat;background-size: contain;"></div>
              </div>
            </div>
            <div class="red-bg"></div>
            <div class="detail">
              <div class="topic">${this.ad.message}</div>
              <div id="hdgg_click_ad_btn" class="goto">${this.ad.receiveText}</div>
            </div><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span><span class="ribbon"></span></div>
        </div>
      </div>
    `);

    console.log('')

    setTimeout(_ => {
      $('#hdgg_show-win .dialog5').append('<span class="close-btn closetc iconfont"></span>');
      
       this.hadnleClose();
    }, 1500)
    
    // if (window.__wxjs_environment === 'miniprogram' ) {
    //   $('#hdgg_click_ad_btn, #hdgg_click_ad_img').on('click', function() {
    //     handleClickFuc();
    //     return false;
    //   })
    // }else{
      
    // }
    this.hadnleClick();
  }

  hadnleClose() {
    $('#hdgg_show-win .close-btn').on('click', () => {
      $('#hdgg_show-win').remove();
      this.close();
    })
  }

  hadnleClick() {
    // if (utils.mpEnv() && !!this.ad.appId) {
    //   $('#hdgg_click_ad_btn').on('click', () => {
    //     this.click({
    //       ad: this.ad,
    //     });
		// 		// utils.copyUrl2()
    //   })
    //   return;
    // }

    $('#hdgg_click_ad_btn, #hdgg_click_ad_img .m-image').one('click', (e) => {
      e.preventDefault();
      this.click({
        ad: this.ad,
      });
			// utils.copyUrl2()
      return false;
    })
  }
}