import './style/capsulePlug.scss';
import Request from '../core/request';
import utils from '../utils/utils'
import Slm from '../modules/slm';
import Conf from '../config/config';
import PrizePDD from '../custom/prizePdd';
import Noaduc from './noAdUc';
import SVGA from 'svgaplayerweb';
import { hdggMaterial, hdggMaterial1 } from '../popup/hdggMaterial2'
import { hdggMaterialFlod, hdggMaterialGuaka } from '../popup/materialNteraction'
const objDake = {
    hdggMaterial,
    hdggMaterial1,
    hdggMaterialFlod,
    hdggMaterialGuaka
}
export default class capSule {
    constructor(options) {
        console.log("", options)
        this.options = options.options;
        this.click = options.options.click;
        this.close = options.options.close;
        this.create();
        this.num = 0;
        this.add_bag = ''
        this.ads = true;
        this.suiArr = null;
        this.setTime = null;
        this.onclickRed();
        this.flag = true;
        this.ad = null,
            this.gameHtmlId = options.options.gameHtmlId
    }


    create() {

        $('body').append(
            `<div no-heatmap class="meng">
                <div class="eggBg">
               
                <div class="clickDan"></div>
                <div class="btnDan"></div>
             </div>
           </div>`
        )
        $('.eggBg').append(`<canvas id="propuCanvas" class="tip" width="750" height="1334"  style="position: absolute;top: -1.3rem;left: 0rem;width: 18.75rem;height: 33.35rem;"></canvas>`)
        let player = new SVGA.Player('#propuCanvas');
        let parser = new SVGA.Parser('#propuCanvas');//IE6+Parser
        parser.load('https://hdggcdn.bayimob.com/hdggstatic/zadanChajian/danji.svga', function (videoItem) {
            player.setVideoItem(videoItem);
            player.startAnimation();
        });
        player.setContentMode("ScaleToFill");
        player.onFinished(() => {
        });

    }
    //
    zadanAlertCommon(imageUrl, advIntro) {
        let str = '<div id="common2Propu"></div>';
        $('.meng').append(str);
        let advBox = '<div style="width:100%;height:100%;position:relative;top:0;left:0;z-index:3;"><div class="allBox"><div class="propuImg"><img src="' + imageUrl + '"/></div><div class="propuText">' + advIntro + '</div><div class="lingqu"></div></div></div><div class="closeBtn1"></div>';
        $('#common2Propu').append(advBox);
        $('.allBox').show();
    }

    //
    onclickRed() {
        var clickNum = 0;
        let _this = this;
        $('body').on('click', '.btnDan,.clickDan', () => {
            if (!this.flag) {
                return
            }
            this.flag = false
            let str = '<canvas id="propuCanvasegg" class="tip" width="750" height="1334"  style="position: absolute;top: -1.3rem;left: 0rem;width: 18.75rem;height: 33.35rem;"></canvas>';
            $('.eggBg').append(str)
            let player = new SVGA.Player('#propuCanvasegg');
            let parser = new SVGA.Parser('#propuCanvasegg');//IE6+Parser
            parser.load('https://hdggcdn.bayimob.com/hdggstatic/zadanChajian/zadan.svga', function (videoItem) {

                player.setVideoItem(videoItem);
                player.fillMode = "Forward"
                player.loops = 1
                player.clearsAfterStop = false;
                $('#propuCanvas').remove()
                player.startAnimation();
            });
            player.setContentMode("ScaleToFill");
            setTimeout(() => {
                Request.getAd({
                    appkey: this.options.appkey,
                    adSpaceKey: this.options.adSpaceKey,
                    uid: this.options.uid,
                    hdggReferer: utils.gamePath,
                    mobileSystem: utils.mobileSystem,
                    browserType: utils.browserType,
                    advLayout: this.options.advLayout,
                    adTriggerType: 'treat',
                    gamehtmlId: this.gameHtmlId
                }).then((ad) => {
                    // resolve(ad)
                    console.log(ad, 'ad');
                    _this.ad = ad;
                    if (ad.advLayout == 1) {
                        this.zadanAlertCommon(ad.imageUrl, ad.advIntro)
                    }
                    if (ad.advLayout == 2) {
                        let params = {
                            ad,
                            closeRed: true,
                            myClass: 'meng',
                            myClick: this.handClick,
                            appkey: this.options.appkey,
                            adSpaceKey: this.options.adSpaceKey,
                            hdggAdvInfoId: ad.id,
                            gameName: this.options.gameName,
                            uid: this.options.uid,
                            sessionId: this.options.sessionId,
                            // issuedTime: ad.issuedTime,
                            issuedTime: new Date().getTime(),
                            province: this.options.province,
                            advMaterialId: ad.advMaterialId,
                            mobileSystem: utils.mobileSystem,
                            imei: this.options.imei,
                            doumobkey: ad.doumobkey,
                            dkey: ad.dkey,
                            type: ad.type,
                            // ad: ad,
                            bidReqId: ad.bidReqId,
                            adPlatformValue: ad.adPlatformValue,
                            hdggReferer: utils.gamePath,
                            sessId: ad.sessId,
                            repeatIssued: ad.repeatIssued,
                            adTriggerType: 'treat',

                            gameHtmlId: this.gameHtmlId,
                            routeToAd: this.options.routeToAd,
                            screenWide: window.screen.width,
                            screenHigh: window.screen.height,
                            screenZoom: 3
                        }
                        // this.click({ad:ad})
                        if (ad.materialImageList && ad.materialImageList.length == 2) {
                            let options = {
                                ad: ad,
                                click: this.click,
                                close: this.close,
                                appkey: this.options.appkey,
                                adSpaceKey: this.options.adSpaceKey,
                                gameName: this.options.gameName,
                                uid: this.options.uid,
                                sessionId: this.options.sessionId,
                                imei: this.options.imei,
                                province: this.options.province,
                                gameHtmlId: this.gameHtmlId
                                // redChajian:''
                            }
                            new objDake.hdggMaterial({
                                options: options,
                                routeToAd: options.routeToAd
                            });
                            $('.meng').remove()
                        }
                        else if (ad.materialImageList && ad.materialImageList.length == 3) {
                            let options = {
                                ad: ad,
                                click: this.click,
                                close: this.close,
                                appkey: this.options.appkey,
                                adSpaceKey: this.options.adSpaceKey,
                                gameName: this.options.gameName,
                                uid: this.options.uid,
                                sessionId: this.options.sessionId,
                                imei: this.options.imei,
                                province: this.options.province,
                                gameHtmlId: this.gameHtmlId
                                // redChajian:''
                            }
                            new objDake.hdggMaterial1({
                                options: options,
                                routeToAd: options.routeToAd
                            });
                            $('.meng').remove()
                        } else {
                            new PrizePDD(params)
                        }

                    }
                    if (ad.advLayout == 5) {

                        let options = {
                            ad: ad,
                            click: this.click,
                            close: this.close,
                            appkey: this.options.appkey,
                            adSpaceKey: this.options.adSpaceKey,
                            gameName: this.options.gameName,
                            uid: this.options.uid,
                            sessionId: this.options.sessionId,
                            imei: this.options.imei,
                            province: this.options.province,
                            redChajian: '',
                            gameHtmlId: this.gameHtmlId
                        }

                        $('.meng,.propur').remove()

                        if (ad.interactiveMaterial.interactiveType == 'FLOP') {
                            new objDake.hdggMaterialFlod({
                                options: options,
                                routeToAd: options.routeToAd,
                            })
                            return
                        }
                        if (ad.interactiveMaterial.interactiveType == 'SHAVE_CARD') {
                            utils.session.set('guaka_drop', true)
                            new objDake.hdggMaterialGuaka({
                                options: options,
                                routeToAd: options.routeToAd,

                            })
                            return
                        }
                    }
                    if (!ad.advLayout) {

                        console.log(this.options.handleWinClose, '')
                        new Noaduc({
                            appkey: this.options.appkey,
                            closeRed: true,
                            myClass: 'meng'
                        })
                    }
                    this.showClick(ad)
                    this.closeClick()
                    this.clickDrop(ad, {
                        appkey: this.options.appkey,
                        adSpaceKey: this.options.adSpaceKey,
                        uid: this.options.uid,
                        hdggReferer: utils.gamePath,
                        mobileSystem: utils.mobileSystem,
                        browserType: utils.browserType,
                        advLayout: this.options.advLayout,
                        adTriggerType: 'treat',
                        gameHtmlId: this.gameHtmlId
                    })
                }).catch((err) => {
                    console.log(err)

                    new Noaduc({
                        appkey: this.options.appkey,
                        closeRed: true,
                        myClass: 'meng',

                    })
                })
            }, 500);
        });
    }
    //
    closeClick() {
        $('.closeBtn1').on('click', () => {
            utils.storage.remove('slmShow')
            $('.meng').remove()
        })

    }
    //
    handClick(ad) {

        console.log(this)
        Request.click({
            statistics: {
                appkey: this.options.appkey,
                adSpaceKey: this.options.adSpaceKey,
                hdggAdvInfoId: ad.id,
                gameName: this.options.gameName,
                uid: this.options.uid,
                sessionId: this.options.sessionId,
                // issuedTime: ad.issuedTime,
                issuedTime: new Date().getTime(),
                province: this.options.province,
                advMaterialId: ad.advMaterialId,
                mobileSystem: utils.mobileSystem,
                imei: this.options.imei,
                doumobkey: ad.doumobkey,
                dkey: ad.dkey,
                type: ad.type,
                // ad: ad,
                bidReqId: ad.bidReqId,
                adPlatformValue: ad.adPlatformValue,
                hdggReferer: utils.gamePath,
                sessId: ad.sessId,
                repeatIssued: ad.repeatIssued,
                adTriggerType: 'treat',
                abGroupId: ad.abGroupId || '',
                abGroupName: ad.abGroupName || '',
                gameHtmlId: this.gameHtmlId || '',
                mediaMaterialId: utils.getUrlParam('mediaMaterialId') ? utils.getUrlParam('mediaMaterialId') : '',
                clickRate: ad.clickRate,
                ctrType: ad.ctrType
            },
            // dspAd: this.ad.dspAd

        }).then(_ => {
            this.options.routeToAd({ ad })
        })
        return
    }
    // 
    showClick(ad) {
        Request.show({
            statistics: {
                appkey: this.options.appkey,
                adSpaceKey: this.options.adSpaceKey,
                hdggAdvInfoId: ad.id,
                gameName: this.options.gameName,
                uid: this.options.uid,
                sessionId: this.options.sessionId,
                // issuedTime: ad.issuedTime,
                issuedTime: new Date().getTime(),
                province: this.options.province,
                advMaterialId: ad.advMaterialId,
                mobileSystem: utils.mobileSystem,
                imei: this.options.imei,
                doumobkey: ad.doumobkey,
                dkey: ad.dkey,
                type: ad.type,
                // ad: ad,
                bidReqId: ad.bidReqId,
                adPlatformValue: ad.adPlatformValue,
                hdggReferer: utils.gamePath,
                sessId: ad.sessId,
                repeatIssued: ad.repeatIssued,
                adTriggerType: 'treat',
                abGroupId: ad.abGroupId || '',
                abGroupName: ad.abGroupName || '',
                gameHtmlId: this.gameHtmlId || '',
                mediaMaterialId: utils.getUrlParam('mediaMaterialId') ? utils.getUrlParam('mediaMaterialId') : '',
                clickRate: ad.clickRate,
                ctrType: ad.ctrType,
                impression_id: utils.getUrlParam('impression_id') ? utils.getUrlParam('impression_id') : ''
            },
            // dspAd: this.ad.dspAd

        }).then(_ => {
            this.flag = true;
        })
    }
    // 
    clickDrop(ad, param) {
        $('.lingqu').on('click', () => {
            console.log(ad, 'ad')
            // 
            this.handClick(ad)
        })
        $('.propuImg').on('click', 'img', () => {
            this.handClick(ad)
        })
    }
}