import './style/moregame.scss'
import Qs from 'qs'
import Ajax from '../utils/ajax'
import Request from '../core/request'
import Digitroll from '../utils/digitroll'
import Utils from '../utils/utils'
import SVGA from 'svgaplayerweb';

export default class MoreGame {
  constructor(options) {
    this.params = {
      appkey: options.appkey,
      adSpaceKey: options.adSpaceKey,
      uid: options.uid,
      imei: options.imei,
      idfa: options.idfa,
      hdgghtmlid: options.hdgghtmlid,
    }
    this.gameType = options.gameType;
    this.number = Utils.session.get('joinNumber') || parseInt(Math.random()*(12000-10000) + 10000);
    this.create();
    this.requestList().then(res => {
      if (res.code == 0) {
        this.render(res)
      }
    });
  }
  //
  ganmeListClick(param, dropUrl) {

    Request.gameListClick(param).then((res) => {
      window.location.href = dropUrl;
      console.log(res + '')
    }).catch((err) => {
      console.log(err + '')
    })
  }
  //
  gameListShow (param) {

    Request.gameListClick(param).then((res) => {
      // console.log(newurl)

      // window.location.href = dropUrl;

      console.log(res + '')
    }).catch((err) => {
      console.log(err + '')
    })
  }

  create() {
    let _this = this;
    if ($('#hdgg_show-tomorrow').length > 0) {
      $('#hdgg_show-tomorrow').remove();
    }
    $('body').append(`
      <div no-heatmap id="hdgg_show-tomorrow">
        <div class="m-wrapper">
          <!--<div class="m-hd"></div>-->
          <div class="m-close"></div>
          <div class="m-num-roll" id="num-roll"></div>
          <div class="m-huan" onclick=""></div>
          <div class="m-content">
            <div class="m-content-inner">

            </div>
          </div>
        </div>
        <!-- <div id="hdgg_wall" style="width: 100%;height:100%;position: absolute;top: 0;left: 0;overflow: hidden;"></div> -->
      </div>
    `)

    $('body').css('overflow-y', 'hidden');
    $('#hdgg_show-tomorrow .m-close').on('click', () => {
      $('body').css('overflow-y', 'auto');
      $('#hdgg_show-tomorrow').remove();
      clearInterval(_this.timer);
    });
    // this.initSvga();
    this.initNums();
  }

  /* 
    
    0.8s1
  */
  initNums() {
    let _this = this;
    let numRoll = new Digitroll({
      container:'#num-roll'
    });
    numRoll.roll(this.number);

    this.timer = setInterval(function () {
      _this.number += 1;
      numRoll.roll(_this.number)
    },800);
    
  };

  initSvga() {
    let player = new SVGA.Player('#hdgg_wall');
    let parser = new SVGA.Parser('#hdgg_wall'); //  IE6+ Parser
    let imgUrl = 'https://hdggcdn.bayimob.com/hdggstatic/common/tuijian_ribbon.svga'
    parser.load(imgUrl, function(videoItem) {
      player.setVideoItem(videoItem);
      player.startAnimation();
    });

    player.loops = "1";
    player.clearsAfterStop = true;
    player.fillMode = "Forward";
    player.onFinished(()=>{
      $("#hdgg_show-tomorrow #hdgg_wall").remove();
    })
  }

  requestList() {
    let params = { ...this.params }
    delete params.hdgghtmlid
    return Ajax({
      url: '/gameList',
      params,
      method: 'get',
    })
  }

  render(res) {
    if (res.code === 0) {
      var games = res.list;
      
      this.appendCard(games);
      //
      let param = {
        event: "list_show",
        appkey: this.params.appkey,
        adSpaceKey: this.params.adSpaceKey,
        uid: this.params.uid,
        
      }
      this.gameListShow(param)
      $(document).on('click', '.goAnother', (e) => {
        e.preventDefault()
        // window.location.href = e.currentTarget.href;
        let dropUrl = e.currentTarget.href;
        console.log(dropUrl)
        let param = {
          event: "list_trunto",
          appkey: this.params.appkey,
          adSpaceKey: this.params.adSpaceKey,
          uid: this.params.uid,
          hdgghtmlid: this.params.hdgghtmlid,
        }
        this.ganmeListClick(param, dropUrl)


      })
      $('#hdgg_show-tomorrow .m-huan').off('click');;
      $('#hdgg_show-tomorrow .m-huan').on('click', () => {
        //id
        this.huan(games);
        let params = { ...this.params }
        delete params.hdgghtmlid
        Ajax({
          url: '/gameList',
          // params: Object.assign({}, this.params, {
          //   hdgghtmlid: this.huan(games),
          // }),
          params,
          method: 'get',
        }).then(res => {
          if (res.code === 0) {
            games = res.list;
            //
            $('#hdgg_show-tomorrow .m-content-inner').empty();
            this.appendCard(games);
          }
        })
      });
    }
  }


  //
  huan(input) {
    var idArr = [];
    for (var i = 0, huanlength = input; i < huanlength.length; i++) {
      idArr.push(huanlength[i].id)
    }
    var hdgghtmlid = idArr.join(',');
    return hdgghtmlid;
  }

  //append
  appendCard(games) {
    games.forEach((n) => {
      if (n.type != this.gameType) {
        console.log(n.url);
        let u = /http/.test(n.url) ? n.url : window.location.protocol+'//'+window.location.hostname+`/${n.url}`;

        $('#hdgg_show-tomorrow .m-content-inner').append(`
          <div class="m-li">
             <a href="${u}&entranceType=4" class="goAnother" style="position:static;width:100%;height:auto;">
              <div style="border-radius: 0.75em;width: 100%;padding: .2em .4em;">
                <img src="https://hdggcdn.bayimob.com/${n.cardImage}" />
              </div>
             </a>
            <!--<p>${n.gameName}</p>-->
            <a href="${u}&entranceType=4" class="goAnother m-goAnother-btn"><span style="font-size: 1.25em"></span></a>
          </div>
        `)
      }
    })
  }

}