import './style/entrance.scss'
import Conf from '../config/config'
import Qs from 'qs'
import Request from '../core/request'
import Utils from '../utils/utils'
import SVGA from 'svgaplayerweb';

// 
export class Entrance {

  constructor(options) {
    console.log(options);
    this.appkey = options.appkey;
    this.adSpaceKey = options.adSpaceKey;
    this.uid = options.uid;
    this.rem = options.rem;
    this.type = options.type;
    this.options = options;
    // 
    if (options.buoy.id == null && !options.buoy.imageUrl) {
      this.init();
      return;
    }
    this.render(options.buoy);
  }

  init() {
    let params = {
      appkey: this.appkey,
      adSpaceKey: this.adSpaceKey,
      entranceType: 3
    }
    console.log(params)
    let paramsStr = Qs.stringify(params);

    let entranceUrl = Conf.floatIcon.entranceUrl;
    let entranceImage = Conf.floatIcon.entranceImage;
    let entranceAbsoluteUrl = Conf.floatIcon.entranceAbsoluteUrl;
    // if (Utils.mpEnv()) {
    //   return;
    // }
    if (Utils.browserType == 1) {
      const wechatEntranceType = Conf.floatIcon.wechatEntranceType;
      if (wechatEntranceType == 2) {
        entranceUrl = Conf.floatIcon.wechatEntranceUrl;
        entranceImage = Conf.floatIcon.wechatEntranceImage;
        entranceAbsoluteUrl = Conf.floatIcon.wechatEntranceAbsoluteUrl;
      } else if (wechatEntranceType == 3) {
        return;
      }
    }

    this.fullpath = entranceAbsoluteUrl || `${Conf.staticURL}/${entranceUrl}/index.html?${paramsStr}`;
    console.log(entranceImage, 'svga')
    let num = Math.floor(Math.random() * 2)
    if (this.type == '580' || this.type == '581') {
      var svgaImg = "https://hdggcdn.bayimob.com/hdggstatic/Zujian/" + entranceImage[1]
    } else {
      var svgaImg = "https://hdggcdn.bayimob.com/hdggstatic/Zujian/" + entranceImage[num]
    }

    let item = `<canvas id="hdgg-left-float-image" class="tip" style="position: absolute;left:0rem;top:7rem;width:5rem; z-index:99" width="200" height='200'></canvas>`;
    $('body').append(item);

    var player = new SVGA.Player('#hdgg-left-float-image');
    var parser = new SVGA.Parser('#hdgg-left-float-image'); //  IE6+ Parser
    parser.load(svgaImg, function (videoItem) {
      player.setVideoItem(videoItem);
      player.startAnimation();
    });
    player.setContentMode("ScaleToFill");

    $('#hdgg-left-float-image').on('click', () => {
      let param = {
        event: "list_icon_click",
        appkey: this.appkey,
        adSpaceKey: this.adSpaceKey,
        uid: this.uid
      }
      this.ganmeListClick(param);
    })
  }

  render(options) {

    if (options.materialType == "1") {
      let imageUrl = `${Conf.assetURL}/${options.imageUrl}`;
      let item = `<div id="hdgg-left-float-image" style='font-size:${this.options.rem}rem;position:fixed;top:6.2em;left:0;z-index:99;width:3.5em;'>
          <img src="${imageUrl}" style="width:100%;" />
        </div>`
      $("body").append(item);
    }

    if (options.materialType == "2") {
      let item = `<canvas id="hdgg-left-float-image" class="tip" style="position: absolute;left:0rem;top:7rem;width:5rem; z-index:99" width="200" height='200'></canvas>`;
      $('body').append(item);
      let materialPath = `${Conf.assetURL}/${options.materialPath}`;
      var player = new SVGA.Player('#hdgg-left-float-image');
      var parser = new SVGA.Parser('#hdgg-left-float-image'); //  IE6+ Parser
      parser.load(materialPath, function (videoItem) {
        player.setVideoItem(videoItem);
        player.startAnimation();
      });
      player.setContentMode("ScaleToFill");
    }
    let event = "buoy_show";
    this.reportEvent(event);
    this.routerLink(options);

  }

  routerLink(options) {

    if (options.associateType == "gameHtmlId") {
      let params = {
        appkey: this.appkey,
        adSpaceKey: this.adSpaceKey,
        entranceType: 3,
        gamehtmlid: options.associateTarget,
        hdgghtmlid: options.associateTarget
      };
      let paramsStr = Qs.stringify(params);
      // let pathname = options.pathname ? options.pathname.replace('static/', '') : "";
      this.fullpath = `${location.origin}/${options.pathname}/index.html?${paramsStr}`;
      // this.fullpath = `${Conf.staticURL}/${pathname}/index.html?${paramsStrs}`;
    };

    if (options.associateType == "link") {
      this.fullpath = options.associateTarget;
    };

    $('#hdgg-left-float-image').on('click', () => {
      let event = "buoy_click";
      this.reportEvent(event);
    });

  }

  reportEvent(event) {
    let params = {
      event: event,
      appkey: this.appkey,
      adSpaceKey: this.adSpaceKey,
      uid: this.uid,
      hdggReferer: Utils.gamePath,
      expansion: {
        gameHtmlId: this.options.gameHtmlId,
        buoyId: this.options.buoy.id
      }
    }
    Request.gameListClick(params).then((res) => {
      console.log(res + '');
      if (event == "buoy_click") {
        window.location.href = this.fullpath;
      }
    }).catch((err) => {
      console.log(err + '')
    });
  }

  ganmeListClick(param) {
    console.log(this.fullpath + 'address')
    Request.gameListClick(param).then((res) => {
      window.location.href = this.fullpath;
      console.log(res + '')
    }).catch((err) => {
      console.log(err + '')
    });
  }

}

//  
export class GameCenter {
  constructor(options) {
    this.options = options;
    this.gameMaterialId = options.material.id;
    this.imageurl = `${Conf.assetURL}/${options.material.imageurl}`;

    this.routeToUrl(options);
    this.create();
  }

  getMaterial() {
    return Request.gameMaterial();
  }

  routeToUrl(options) {

    if (options.material.relationType == "2") {
      this.fullpath = options.material.relationLink;
    }

    if (options.material.relationType == "1") {
      let pathname = options.material.pathname ? options.material.pathname.replace('static/', '') : "";
      let params = {
        gamehtmlid: options.material.gamehtmlid,
        hdgghtmlid: options.material.gamehtmlid,
        appkey: options.appkey,
        adSpaceKey: options.adSpaceKey,
        entranceType: 5,
        imei: options.imei,
        idfa: options.idfa,
      }
      let paramsStr = Qs.stringify(params);
      this.fullpath = `${location.origin}/${options.material.pathname}/index.html?${paramsStr}`;
      // this.fullpath = `${Conf.staticURL}/${pathname}/index.html?${paramsStr}`;
    };
  }

  create() {
    if ($('#hdgg_more_game_award').length > 0) {
      return;
    }
    $('body').append(`
      <div no-heatmap id="hdgg_more_game_award">
        <img style="display: block;width:100%;" src="${this.imageurl}" />
      </div>
    `);
    $('#hdgg_more_game_award').on('click', this.routeTo.bind(this));
    this.createAnimate().then(_ => {
      this.materialShow();
    });
  }

  createAnimate() {

    return new Promise((resolve, reject) => {

      setTimeout(function () {

        $('#hdgg_more_game_award').animate({
          "z-index": "99",
        }, 0, function () {
          $('#hdgg_more_game_award').animate({
            'bottom': '0.5em',
          }, 1800, function () {
            $('#hdgg_more_game_award').css("z-index", "1");
            resolve()
          });
        })
      }, 200);
    })
  }

  materialShow() {
    Request.materialShow({
      gameMaterialId: this.gameMaterialId,
    });
  }

  routeTo() {
    this.materialClick().then(_ => {
      window.location.href = this.fullpath;
    })
  }

  materialClick() {
    return Request.materialClick({
      gameMaterialId: this.gameMaterialId
    });
  }
}