import './style/customPrize.scss'
import Request from '../core/request'
import Utils from '../utils/utils'

export class CustomPrize {

    constructor(options) {
      this.params = {
        appkey: options.appkey,
        adSpaceKey: options.adSpaceKey,
        uid: options.uid,
        receiveId:options.receiveId,
        deviceId:options.deviceId,
        userId: options.userId,
        os: options.os,
        appName: options.appName,
      }
      this.getCoustomInfo(this.params);
    }

    getCoustomInfo(param) {
        let imgUrl;
        Request.getCustomInfo(param).then((res)=>{
            console.log(res);
            if(res.code == "0"){
                imgUrl = "https://hdggcdn.bayimob.com/hdggstatic/custom/success.png"
                this.changeRecodeStatus(param.receiveId);
            }else if(res.msg == "" ){
                imgUrl = "https://hdggcdn.bayimob.com/hdggstatic/custom/repeat.png"
            }else{
                imgUrl = "https://hdggcdn.bayimob.com/hdggstatic/custom/fail.png"
            }
            this.create(imgUrl);
        }).catch((err)=>{
            imgUrl = "https://hdggcdn.bayimob.com/hdggstatic/custom/fail.png"
            this.create(imgUrl);
        });
    };
  
    create(imgUrl) {

        if($("#dialog5").length > 0){
            $('#dialog5').remove();
        }

        if ($('.hdgg_custom_price').length > 0) {
            $('.hdgg_custom_price').remove();
        }

        $('body').append(`
            <div no-heatmap class="hdgg_custom_price">
                <div class="hdgg_custom_price_box">
                    <div class="content">
                        <img src="${imgUrl}" alt="">
                    </div>
                    <div class="hdgg_custom_price_button">
                        <img src="https://hdggcdn.bayimob.com/hdggstatic/custom/button.png" alt="">
                    </div>
                </div>
            </div>
        `)
        $('body').css('overflow-y', 'hidden');
        $('.hdgg_custom_price .hdgg_custom_price_button').on('click', () => {
            $('body').css('overflow-y', 'auto');
            $('.hdgg_custom_price').remove();
        })
    }

    changeRecodeStatus(receiveId) {
        let adsRecord = Utils.storage.get("adsRecord");
        for(let i in adsRecord){
            if(receiveId == adsRecord[i].receiveId){
                adsRecord[i].isTake = true;
                Utils.storage.set("adsRecord",adsRecord);
            }
        }
    }
}