import './style/txEntrance.scss'
import Utils from '../utils/utils'

export default class flyPrize {
  constructor(options) {
    this.rem = options.rem;
    this.flyTop = options.flyTop;
    this.init();
  }

  init() {
    // if (Utils.mpEnv()) {
    //   return;
    // }
    $(`
      <a id="fly-prize" href="javascript:;" style='font-size:${this.rem}rem;position:fixed;top:42%;right:50%;z-index:99;width:3.4rem;height:1.85rem;margin-right: -1.7rem;'>
        <img src="https://hdggcdn.bayimob.com/hdggstatic/activity/fly.png" style="width:100%;" />
      </a>`).appendTo($('body'));
    setTimeout(() => {
      this.flyTop ? this.fly() : this.fly2();
    }, 50)
    setTimeout(() => {
      $('#fly-prize').remove();
    }, 1050)
  }
  fly() {
    $('.jiang').append(`<img src="https://hdggcdn.bayimob.com/hdggstatic/yaoshaizi7/star.png" class="star" style="width:40%;margin-top:0.75rem;">`);
    $('.jiang').append(`<img src="https://hdggcdn.bayimob.com/hdggstatic/yaoshaizi7/star.png" class="star1" style="width:35%;">`);
    $('.jiang').append(`<img src="https://hdggcdn.bayimob.com/hdggstatic/yaoshaizi7/star.png" class="star2" style="width:30%;margin-top:-0.25rem;">`);
    $('#fly-prize').animate({
      top: '1.25rem',
      right: '3.5rem',
      width: '0'
    }, 1000, function () {
      $('.star').addClass("addw");
      $('.star1').addClass("addw");
      $('.star2').addClass("addw");
      $('.jiang').addClass('jiangw')
      setTimeout(() => {
        $('.jiang').empty().removeClass('jiangw');
      }, 500)
    })
  }
  fly2() {
    $('#fly-prize').animate({
      top: '90%',
      right: '3.5rem',
      width: '0'
    }, 1000)
  }
}