
import { hdggMaterial } from './hdggMaterial2';
import Utils from '../utils/utils'
 import './style/popupWindow.scss'
export default class propWindow {
    constructor(options) {
       this.pageNum = options.options.num;
       this.click = options.options.click
       this.created()
    }




    created() {
        $('body').append(`
        <div class="propWindow">
  <div class="propWindowImg">
    <div class="propWindowClose"></div>
    <div class="propWindowGo"></div>
  </div>
</div>
        `);
            //
            $('.propWindowClose').on('click',()=>{
              $('.propWindow').remove()
              Utils.storage.remove('curentPageCloseNum'+this.pageNum)
              if(Utils.storage.get('leftTimes')=='0') {
                $('.hdgg_show-tomorrow').show()
            }
            })
            //
            $('.propWindowGo').on('click',()=>{
                Utils.storage.remove('curentPageCloseNum'+this.pageNum)
                let params = {

                }
                params.ad = Utils.storage.get("adsRecord")[0];
               this.click(params)
            })
    }
    
}