import './style/noAd.scss'

export default class noAd {
  constructor(options) {
    this.close = options.close;
    this.create();
    this.handleClick();
  }

  create() {
    //
    if ($('#hdgg_show-noad').length > 0) {
      $('#hdgg_show-noad').remove();
    }

    $('body').append(`
      <div no-heatmap id="hdgg_show-noad" class="m-hdgg-no-ad">
        <div class="m-close" style="display: none;"></div>
        <!--<div class="sogou-title" style="position:absolute;top:6.5em;left:50%;width:92%;height:3.77em;margin-left:-46%;background:url('https://hdggcdn.bayimob.com/hdggstatic/sougou/sgtxt.png') 0 0 no-repeat;background-size:contain;z-index:1;"></div>-->
        <!--<div class="sogou-guang"></div>--> 
        <div class="m-wrapper" style="opacity: 1;">
        <div id="sogou_wap_1005927" style="width: 94%;margin-left:3%;position:absolute;top:33%;"></div>
        <div id="sogou_wap_1005916" style="width: 94%;margin-left:3%;position:absolute;top:56%;"></div>
        <!--<div class="m-box">
          <div id="hdgg_click_ad_btn" class="m-btn">${__("")}</div>
          </div>-->
        </div>
      </div> 
    `)

    setTimeout(() => {
      if(navigator.userAgent.indexOf('iPhone')>-1){
        if(screen.height == 568 && screen.width == 320){
          $('#sogou_wap_1005927').css('top','35%');
          $('#sogou_wap_1005916').css('top','54%');
          $('.m-wrapper').css('top','-2em');
        }
      }
      $('#hdgg_show-noad .m-wrapper').addClass('hdgg_result_show_animation').css('opacity', 1);
      // let sou_gou = document.createElement('script');
      // sou_gou.src = 'https://wds.inte.sogoucdn.com/wap/js/aw.js';
      // $('body').append(sou_gou);
      let sogou_div = document.getElementById("sogou_wap_1005927");
      window.sogou_un = window.sogou_un || [];
      window.sogou_un.push({
        id: "1005927",
        ele: sogou_div
      });
      let sogou_div2 = document.getElementById("sogou_wap_1005916");
      // window.sogou_un = window.sogou_un || [];
      window.sogou_un.push({
        id: "1005916",
        ele: sogou_div2
      });
    }, 0)
    setTimeout(() => {
      $('#hdgg_show-noad .m-close').show();
      $('.sogou-guang').addClass('hdgg_result_show_animation_guang').show();
    }, 600)
  }
  handleClick() {
    $('#hdgg_show-noad .m-close, #hdgg_click_ad_btn').on('click', () => {
      $(this).hide();
      this.close();
      $('#hdgg_show-noad .m-wrapper').addClass('hdgg_result_hide_animation').css('opacity', 0);
      $('.sogou-guang').hide().removeClass('hdgg_result_show_animation_guang');
      setTimeout(function () {
        $('#hdgg_show-noad').remove();

        if (!!window.styleReset && typeof window.styleReset == 'function') {
          try {
            window.styleReset();
          } catch (err) {}
        }
      }, 400)
    })
  }
}