import './style/rules.scss'

export default class Rules{
  constructor(options) {
    this.options = options.options;

    this.handleClick();
  }

  handleClick() {
    $(this.options.rulesEle).on('click', () => {
      if($('#hdgg_rules-wrapper').length == 0) {
        this.create();
      }
      $('#hdgg_rules-wrapper').show();

      $('body').css('overflow', 'hidden')
      setTimeout(() => {
        $('#hdgg_rules-wrapper .m-rules-inner').addClass('rules-show')
      }, 0)

      this.handleClose();
    })
  }

  create() {
    $('body').append(`
      <div no-heatmap id="hdgg_rules-wrapper">
        <div class="m-rules-inner">
          <div class="m-close"></div>
          <div class="m-rules-ct">
            
          </div>
        </div>
      </div>
    `)
    console.log('')

    if(this.options.rules) {
      this.options.rules.forEach((n) => {
        $('#hdgg_rules-wrapper .m-rules-ct').append(
          `<p>${n}</p>`
        )
      })
      console.log('')
    }
  }

  handleClose() {
    $('#hdgg_rules-wrapper .m-close').on('click', () => {
      $('#hdgg_rules-wrapper .m-rules-inner').removeClass('rules-show')
      setTimeout(() => {
        $('#hdgg_rules-wrapper').hide();
      }, 300)
      $('body').css('overflow', 'auto')
    })
  }
}