export default class info{
    constructor(options) {
        this.creatInfo();
        this.options = options
        console.log(this.options,'')
    }
    // <p>Copyright  2012 - 2017 doumob Inc. All Rights Reserved ICP13043905-13 </p>
    // <p style="margin-top: 5px;"> </p>
    creatInfo(){
        setTimeout(() => {
            var html = `
        <div class="record_info" style="padding: 10px 20px;text-align: center;background: rgba(0,0,0,.2);color: rgba(255,255,255,.5);position: absolute;width:100%;line-height:20px;box-sizing:border-box;font-size:15px">
             ${this.options.footerText}
        </div>`

        $("body").append(html);
        var height = document.body.scrollHeight
        $(".record_info").css({"top":height});
        $("body").css({"overflow":"auto"}); 
        },400);
       
    }
}