import './style/inspire.scss'

import Request from '../core/request'
import Utils from '../utils/utils'
import SVGA from 'svgaplayerweb';

export default class Inspire {
  constructor(type) {
    this.type = type;
  }

  /**
   * 
   * 
   *   inspireMessage
  */
  request(params) {
    // let params = this.options;
    Request.getInspireInfo(params).then((res) => {

      if (res.result && !res.result.isWin) {
        Utils.session.remove('inspireMessage');
        return;
      };

      if (res.result && res.result.isWin) {
        Utils.session.set('inspireMessage', res.result.message);
      };
    }).catch((err) => {
      Utils.session.remove('inspireMessage');
    });
  };

  /**
   * 
   * 
  */
  create() {
    let inspireMessage = Utils.session.get('inspireMessage');
    if ($('.hdgg_inspire').length > 0) {
      $('.hdgg_inspire').remove();
    }
    let inspireHtml = `<div no-heatmap class="hdgg_inspire">
      <div id="inspire" class="inspire">
      </div>
      <div class="tips">
        <p>${inspireMessage}</p>
      </div>
      <span></span>
    </div>`;
    $("body").append(inspireHtml);
    let playerInspire = new SVGA.Player('#inspire');
    let parserInspire = new SVGA.Parser('#inspire'); //  IE6+ Parser
    parserInspire.load('https://hdggcdn.bayimob.com/hdggstatic/inspire/inspire.svga', function (videoItem) {
      playerInspire.setVideoItem(videoItem);
      playerInspire.startAnimation();
    });
    playerInspire.loops = "1";
    playerInspire.clearsAfterStop = false;
    playerInspire.fillode = "Forward";
    playerInspire.onFinished(function () {
      $(".hdgg_inspire .inspire").remove();
      $(".hdgg_inspire .tips").show();
      // setTimeout(function () {
      //   $(".hdgg_inspire span").show();
      // }, 800);
    });
    Utils.session.remove('inspireMessage');
    this.close();
  };

  close() {
    setTimeout(() => {
      $('.hdgg_inspire').remove();
    }, 3000);
    $('.hdgg_inspire span').on('click', () => {
      $('.hdgg_inspire').remove();
    });
  }
}